import {
  Button,
  Divider,
  Grid,
  IconButton,
  makeStyles,
  Paper,
  TextField,
  Typography,
} from "@material-ui/core";
import React from "react";
import { Form, Field } from "react-final-form";
import { FieldArray } from "react-final-form-arrays";
import arrayMutators from "final-form-arrays";
import { useDispatch, useSelector } from "react-redux";
import { addPriceUrl, complexListUrl, sportsListUrl, generateSnackData, pricingDetailsUrl, updatePriceUrl } from "../../assets/helper/constants";
import { commonGetRequest } from "../../redux/features/Auth";
import SelectPricing from "../common/SelectPricing";
import { setSnackBar, setIsLoading } from "../../redux/features/Auth";
import Server from "../../Server";
import ControlPointIcon from "@material-ui/icons/ControlPoint";
import { useHistory } from "react-router";
import CourtButton from "../common/CourtButton2";
const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(3),
    backgroundColor: "#F5F5F5",
    borderColor: "#fff",
    display: "flex",
  },
  paper2: {
    padding: theme.spacing(2),
    marginLeft: theme.spacing(3),
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    backgroundColor: "#F3F3F3",
  },
  typo1: {
    color: "#484848",
    verticalAlign: "middle",
    fontWeight: "bold",
  },
  typo2: {
    paddingBottom: theme.spacing(1),
    color: "#484848",
    verticalAlign: "middle",
    fontWeight: "bold",
  },
  typo3: {
    paddingBottom: theme.spacing(1),
    color: "#808080",
    verticalAlign: "middle",
  },
  leftMargin: {
    marginLeft: theme.spacing(3),
    marginTop: theme.spacing(2),
  },
  divider: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  grid1: {
    display: "flex",
  },
  right: {
    marginLeft: "auto",
  },
  icon: {
    color: "#272727",
    marginLeft: "auto",
  },
  textfield: {
    backgroundColor: "#fff",
    width: "95%",
    marginBottom: theme.spacing(1),
  },
  paddingLeft: {
    paddingLeft: theme.spacing(3),
  },
  button: {
    borderRadius: 50,
    marginTop: theme.spacing(2),
    backgroundColor: theme.palette.base1.main,
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base1.dark,
    },
  },
}));
const AddEditPricingForm = () => {
    const history = useHistory();
    const classes = useStyles();
    const required = (value) => (value ? undefined : "* Required");
    const { sports_list, complex_list, pricing_details, isLoading } = useSelector(
      (state) => state.auth
    );
  let [globalForm, setGlobalForm] = React.useState({});
  let [pricingId, setPricingId] = React.useState(0);
  let [selectedTurfId, setSelectedTurfId] = React.useState(0);

  React.useEffect(() => {
    if (history.location.state) {
      setPricingId(history.location.state.id);
      dispatch(
        commonGetRequest({
          url: pricingDetailsUrl,
          key: "pricing_details",
          query: "?id=" + history.location.state.id
        })
      );
    } else {
        setPricingId(0);
    }
  }, [dispatch, history.location.state])

  React.useEffect(() => {
        if(pricingId) {
            setGlobalForm({
                id: pricing_details.id, 
                sports_id: pricing_details.sports_id,
                complex_id: pricing_details.complex_id,
                from_day: pricing_details.from_day,
                to_day: pricing_details.to_day,
                slots: [
                    {
                    start_time: pricing_details.start_time,
                    end_time: pricing_details.end_time,
                    hourly_pricing: pricing_details.hourly_pricing,
                    slot_extension_pricing: pricing_details.slot_extension_pricing,
                    volume_discount: pricing_details.volume_discount,
                    discount: pricing_details.discount
                    }
                ]
            });
            setSelectedTurfId(pricing_details.complex_id);
        } else {
            setGlobalForm({
                sports_id: "",
                complex_id: "",
                from_day: "",
                to_day: "",
                slots: [
                    {
                    start_time: "",
                    end_time: "",
                    hourly_pricing: "",
                    slot_extension_pricing: "",
                    volume_discount: "",
                    discount: ""
                    }
                ]
            });
        }
  }, [pricing_details])

  const onSubmit = async (values) => {
      console.log(values);
    //   dispatch(setIsLoading());
      try {
        let res = {};
          if(pricingId) {
            res = await Server.post(updatePriceUrl, values);              
          } else {
            res = await Server.post(addPriceUrl, values);
          }
  
        if (res.data && res.data.status) {
            dispatch(setSnackBar(generateSnackData(true, res.data.message, "success")));
            if(!pricingId)
                setGlobalForm({
                    sports_id: "",
                    complex_id: "",
                    from_day: "",
                    to_day: "",
                    slots: [
                        {
                        start_time: "",
                        end_time: "",
                        hourly_pricing: "",
                        slot_extension_pricing: "",
                        volume_discount: "",
                        discount: ""
                        }
                    ]
                });
        } else {
            dispatch(setSnackBar(generateSnackData(true, res.data.message, "error")));
        }
        // dispatch(setIsLoading());
      } catch (error) {
        const resp =
          error.response && error.response.data.message
            ? error.response.data.message
            : "Couldn't connect to our servers.";
  
        dispatch(setSnackBar(generateSnackData(true, resp, "error")));
        // dispatch(setIsLoading());
      }
  };
  const weekData = [
    {
      id: "Monday",
      name: "Monday",
    },
    {
      id: "Tuesday",
      name: "Tuesday",
    },
    {
      id: "Wednesday",
      name: "Wednesday",
    },
    {
      id: "Thursday",
      name: "Thursday",
    },
    {
      id: "Friday",
      name: "Friday",
    },
    {
      id: "Saturday",
      name: "Saturday",
    },
    {
      id: "Sunday",
      name: "Sunday",
    },
  ];
  const dispatch = useDispatch();
  React.useEffect(() => {
    dispatch(
      commonGetRequest({
        url: sportsListUrl,
        key: "sports_list",
      })
    );
    dispatch(
      commonGetRequest({
        url: complexListUrl,
        key: "complex_list",
        key_name: "playground_name",
      })
    );
  }, []);
  const package_select_turf = ({ input, meta }) => {
    return (
      // <SelectPricing
      //   data={complex_list}
      //   funct="select"
      //   title="Select Turf/Court"
      //   input={input}
      //   meta={meta}
      //   returnValue={(value) => console.log(value, input)}
      // />
      <>
        <Typography className={classes.typo2}>Select Turf/Court</Typography>
        <CourtButton turfData={complex_list} selectedTurfId={selectedTurfId} getSelectedID={(id) => {
          setGlobalForm({...globalForm, complex_id: id}); 
          setSelectedTurfId(id);
          console.log(globalForm)}
        } />
      </>
    );
  };
  const package_select_sport = ({ input, meta }) => {
    return (
      <SelectPricing
        data={sports_list}
        funct="select"
        title="Select Sport"
        input={input}
        meta={meta}
        returnValue={(value) => console.log(value, input)}
      />
    );
  };
  const package_select_from_day = ({ input, meta }) => {
    return (
      <SelectPricing
        data={weekData}
        funct="from"
        title="From"
        input={input}
        meta={meta}
        returnValue={(value) => console.log(value, input)}
      />
    );
  };
  const package_select_to_day = ({ input, meta }) => {
    return (
      <SelectPricing
        data={weekData}
        funct="to"
        title="To"
        input={input}
        meta={meta}
        returnValue={(value) => console.log(value, input)}
      />
    );
  };
  const package_slot_start_time = ({ input, meta }) => {
    return (
      <TextField
        className={classes.textfield}
        id="start_time"
        label="6:00 PM"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
            input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const package_slot_end_time = ({ input, meta }) => {
    return (
      <TextField
        className={classes.textfield}
        id="end_time"
        label="6:00 PM"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
            input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const package_slot_hourly_price = ({ input, meta }) => {
    return (
      <TextField
        className={classes.textfield}
        id="hourly_price"
        label="Hourly Price"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
            input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const package_slot_extension_pricing = ({ input, meta }) => {
    return (
      <TextField
        className={classes.textfield}
        id="extension_pricing"
        label="Extension Pricing"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
            input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const package_slot_volume_discount = ({ input, meta }) => {
    return (
      <TextField
        className={classes.textfield}
        id="volume_discount"
        label="Volume Discount"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
            input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const package_slot_discount = ({ input, meta }) => {
    return (
      <TextField
        className={classes.textfield}
        id="discount"
        label="Discount"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
            input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  return (
    <>
      {!isLoading ? (
        <Form
          initialValues={globalForm}
          onSubmit={onSubmit}
          mutators={{
            ...arrayMutators,
          }}
          render={({
            handleSubmit,
            submitting,
            values,
            form: {
              mutators: { push, pop },
            },
          }) => (
            <form onSubmit={handleSubmit}>
              {globalForm ? (
                <input type="hidden" name="id" value={globalForm.id} />
              ) : null}
              <Grid>
                <Grid>
                  <Paper className={classes.paper} variant="outlined">
                    <Typography variant="h6" className={classes.typo1}>
                      Pricing Details
                    </Typography>
                  </Paper>
                </Grid>
                <Divider className={classes.divider} />
                <Grid className={classes.paddingLeft}>
                  {/* <Field
                    className={classes.textField}
                    name="complex_id"
                    component={package_select_turf}
                    validate={required}
                  /> */}
                  <Field
                    className={classes.textField}
                    name="sports_id"
                    component={package_select_sport}
                    validate={required}
                  />
                </Grid>
                <Divider className={classes.divider} />
                <Grid className={classes.leftMargin} lg={6}>
                  <Field
                    className={classes.textField}
                    name="complex_id"
                    component={package_select_turf}
                    validate={required}
                  />
                </Grid>
                <Divider className={classes.divider} />
                <Grid
                  lg={6}
                  md={12}
                  className={(classes.grid1, classes.paddingLeft)}
                >
                  <Grid>
                    <Field
                      className={classes.textField}
                      name="from_day"
                      component={package_select_from_day}
                      validate={required}
                    />
                    <Field
                      className={classes.textField}
                      name="to_day"
                      component={package_select_to_day}
                      validate={required}
                    />
                  </Grid>
                  {!pricingId ?
                        <Grid className={classes.right}>
                        <IconButton
                        onClick={() => push("slots", undefined)}
                        className={classes.icon}
                        aria-label="create pricing"
                        component="span"
                        >
                        <ControlPointIcon />
                        <Typography className={classes.typo2}>
                            ADD SLOTS
                        </Typography>
                        </IconButton>
                    </Grid>
                    : 
                  null}
                </Grid>
                {/* <Grid lg={6} md={12}>
                                <Paper className={classes.paper2} elevation={0}> */}
                <FieldArray name="slots">
                  {({ fields }) =>
                    fields.map((name, index) => (
                      <Grid lg={6} md={12}>
                        <Paper className={classes.paper2} elevation={0}>
                          <Grid lg={12} className={classes.grid1}>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                Start Time
                              </Typography>

                              <Field
                                name={`${name}.start_time`}
                                component={package_slot_start_time}
                              />
                            </Grid>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                End Time
                              </Typography>
                              <Field
                                name={`${name}.end_time`}
                                component={package_slot_end_time}
                              />
                            </Grid>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                Hourly Pricing
                              </Typography>
                              <Field
                                name={`${name}.hourly_pricing`}
                                component={package_slot_hourly_price}
                              />
                            </Grid>
                            {index !== 0 ? 
                                <span
                                    onClick={() => fields.remove(index)}
                                    style={{ cursor: "pointer" }}
                                    >
                                    ❌
                                </span>
                            : null}
                          </Grid>
                          <Grid lg={12} className={classes.grid1}>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                Extension Pricing
                              </Typography>

                              <Field
                                name={`${name}.slot_extension_pricing`}
                                component={package_slot_extension_pricing}
                              />
                            </Grid>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                Volume Discount
                              </Typography>
                              <Field
                                name={`${name}.volume_discount`}
                                component={package_slot_volume_discount}
                              />
                            </Grid>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                Discount
                              </Typography>
                              <Field
                                name={`${name}.discount`}
                                component={package_slot_discount}
                              />
                            </Grid>
                          </Grid>
                        </Paper>
                        <Divider />
                      </Grid>
                    ))
                  }
                </FieldArray>
                <Grid lg={6}
                  md={12}
                  className={(classes.grid1, classes.paddingLeft)}>
                    <Button type="submit" className={classes.button} disabled={submitting ? submitting : false}>
                      Save
                    </Button>
                </Grid>
              </Grid>
            </form>
          )}
        />
      ) : null}
    </>
  );
};

export default AddEditPricingForm;
