import React from "react";
import { useHistory } from "react-router-dom";
import PropTypes from "prop-types";
import AppBar from "@material-ui/core/AppBar";
import CssBaseline from "@material-ui/core/CssBaseline";
import Divider from "@material-ui/core/Divider";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import Collapse from "@material-ui/core/Collapse";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import ExpandLess from "@material-ui/icons/ArrowDropDown";
import ExpandMore from "@material-ui/icons/ArrowDropUp";
import MenuIcon from "@material-ui/icons/Menu";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import Logo from "../assets/images/logo.svg";

import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import { Tooltip } from "@material-ui/core";

import { signout } from "../assets/helper/auth";
import { siteUrl } from "../assets/helper/constants";

const drawerWidth = 280;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  drawer: {
    [theme.breakpoints.up("sm")]: {
      width: drawerWidth,
      flexShrink: 0,
      background: theme.palette.base1.main,
      color: "#fff",
    },
  },
  divider: {
    background: "#505771",
    height: "2.2px",
  },
  appBar: {
    [theme.breakpoints.up("sm")]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up("sm")]: {
      display: "none",
    },
  },
  listItemText: {
    marginLeft: theme.spacing(3),
  },
  multiListItemText: {
    marginLeft: theme.spacing(6),
  },
  logoImage: {
    marginLeft: theme.spacing(5),
    marginBottom: theme.spacing(4),
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
    background: theme.palette.base1.main,
    color: "#fff",
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));

function ResponsiveDrawer(props) {
  const classes = useStyles();
  const theme = useTheme();
  const history = useHistory();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [open, setOpen] = React.useState({});
  const [refresh, setRefresh] = React.useState(0);
  const [moduleName, setModuleName] = React.useState(props.admin ? "Vendor" : "Slots");

  const changePath = (directory, name) => {
    setModuleName(name);
    history.push(directory);
  };

  const handleClick = (i) => {
    let newData = open;
    newData[i] = !newData[i];
    setOpen(newData);
    setRefresh(refresh + 1);
  };

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleSignOut = () => {
    console.log("coming here");
    signout(() => {
      // history.go(0);
      window.location = siteUrl
    });
  };

  const items = [
    {
      name: "Schedule",
      hasChild: 1,
      child: [
        {
          name: "Slots",
          redirect: "/slots",
        },
        {
          name: "Bulk Booking",
          redirect: "/bulk",
        },
        {
          name: "All Booking",
          redirect: "/allbooking",
        },
      ],
    },
    {
      name: "Pricing",
      hasChild: 0,
      redirect: "/pricing",
    },
    {
      name: "Members",
      hasChild: 1,
      child: [
        {
          name: "Members",
          redirect: "/members",
        },
        {
          name: "Membership Packages",
          redirect: "/members/packages",
        },
      ],
    },
    {
      name: "Offers",
      hasChild: 0,
      redirect: "/offers",
    },
    {
      name: "Report",
      hasChild: 0,
      redirect: "/report",
    },
    {
      name: "Profile",
      hasChild: 0,
      redirect: "/onboarding_form",
    },
  ];

  const drawer = (
    <div>
      <div className={classes.toolbar} />
      <img
        src={Logo}
        alt={"Playmaker Logo"}
        className={classes.logoImage}
        height={"89.8"}
        width={"154"}
      />
      <Divider className={classes.divider} />
      <List>
        {!props.admin ? items.map((item, i) =>
          item.hasChild ? (
            <div>
              <ListItem key={i} button onClick={() => handleClick(i)}>
                <ListItemText
                  className={classes.listItemText}
                  primary={item.name}
                />
                {open[i] ? <ExpandLess /> : <ExpandMore />}
              </ListItem>
              <Collapse in={open[i]} timeout={"auto"} unmountOnExit>
                <List component="div" disablePadding>
                  {item.child.map((ele, j) => {
                    return (
                      <ListItem
                        key={i + j}
                        button
                        onClick={() => changePath(ele.redirect, ele.name)}
                      >
                        <ListItemText
                          className={classes.multiListItemText}
                          primary={ele.name}
                        />
                      </ListItem>
                    );
                  })}
                </List>
              </Collapse>
            </div>
          ) : (
            <ListItem
              key={i}
              button
              onClick={() => changePath(item.redirect, item.name)}
            >
              <ListItemText
                className={classes.listItemText}
                primary={item.name}
              />
            </ListItem>
          )
        ) : null}
      </List>
    </div>
  );

  const container =
    props.window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar} color={"default"}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <Typography component="div" variant="h6" noWrap>
            <Box color="#272727" fontWeight="fontWeightBold">
              {moduleName}
            </Box>
          </Typography>
          <Box sx={{ flexGrow: 1 }} />
          <Box sx={{ display: { xs: "flex" } }}>
            <Tooltip title="Sign Out">
              <IconButton
                onClick={handleSignOut}
                color="primary"
                aria-label="SignOut"
                component="button"
              >
                <ExitToAppIcon />
              </IconButton>
            </Tooltip>
          </Box>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === "rtl" ? "right" : "left"}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
    </div>
  );
}

ResponsiveDrawer.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default ResponsiveDrawer;
