import React from "react";
import { useHistory } from "react-router";
import { useDispatch } from "react-redux";

import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";

import { Grid, Tooltip } from "@material-ui/core";
import { IconButton } from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";

import Calendar from "./Calendar";

import Button from "../../common/Button/BlueContainedRounded";

import { setBookingConfirm } from "../../../redux/features/Auth";

const useStyles = makeStyles((theme) => ({
  root: {
    position: "fixed",
  },
  cardContent: {
    padding: "none !important",
  },
  typo1: {
    fontWeight: 600,
  },
  typo2: {
    fontSize: "17px",
    fontWeight: 600,
    color: "#272727",
  },
  typo3: {
    fontSize: "12px",
    color: "#272727",
  },
  paper1: {
    backgroundColor: "#fff",
  },
  paper2: {
    margin: theme.spacing.unit, // You might not need this now
    position: "fixed",
    bottom: theme.spacing.unit * 0,
    right: theme.spacing.unit * 2,
  },
  paper3: {
    margin: theme.spacing(5),
    backgroundColor: "#f3f3f3",
  },
  grid: {
    padding: theme.spacing(2),
  },
  icon1: {
    marginLeft: theme.spacing(2),
    paddingTop: theme.spacing(1),
    paddingRight: theme.spacing(1),
  },
}));

const ShowAvail = (props) => {
  const { formData } = props;
  console.log('formData :>> ', formData);
  const dispatch = useDispatch();
  const history = useHistory();
  const classes = useStyles();
  const bookingConfirm = () => {
    const unique_id = Math.random().toString(36).slice(2);
    dispatch(setBookingConfirm({ unique_id, bulk: true, ...formData }));
    history.push("/checkout?unique_id=" + unique_id);
  };
  return (
    <div className={classes.root}>
      <Paper variation="outlined" className={classes.paper1}>
        <Grid container alignContent="center" alignItems="center">
          <Tooltip title="Close">
            <IconButton aria-label="Close" component="span">
              <CloseIcon fontSize="medium" />
            </IconButton>
          </Tooltip>
          <Typography className={classes.typo1} variant="h6">
            Availability
          </Typography>
        </Grid>
      </Paper>
      <Calendar {...props} />
      <Paper className={classes.paper3}>
        <Grid alignItems="center" alignContent="center" className={classes.grid}>
          <Grid>
            <Typography className={classes.typo2} component="div">
              Selected slots status
            </Typography>
          </Grid>
          <Grid container justifyContent="space-between">
            <Grid>
              {" "}
              <Typography className={classes.typo2} component="div">
                30
              </Typography>
              <Typography className={classes.typo3} component="div">
                Required Slots
              </Typography>
            </Grid>
            <Grid>
              {" "}
              <Typography className={classes.typo2} component="div">
                30
              </Typography>
              <Typography className={classes.typo3} component="div">
                Available Slots
              </Typography>
            </Grid>
            <Grid>
              {" "}
              <Typography className={classes.typo2} component="div">
                30
              </Typography>
              <Typography className={classes.typo3} component="div">
                Unavailable Slots
              </Typography>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
      <Paper className={classes.paper2}>
        <Button handleclick={() => bookingConfirm} title="Continue" />
      </Paper>
    </div>
  );
};

export default ShowAvail;
