import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";

// Components
import Select from "../../common/Select";
import DatePicker from "../../common/DatePicker";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(3),
    textAlign: "left",
    color: theme.palette.text.secondary,
  },
  button: {
    borderRadius: 50,
    backgroundColor: theme.palette.base1.main,
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base1.dark,
      color: "#fff",
    },
  },
}));

export default function Header(props) {
  const { changeState, data } = props;
  const [formData, setFormData] = React.useState({
    key: null,
    date: new Date(),
  });
  const [checkData, setCheckData] = React.useState(0);
  const classes = useStyles();
  const sportData = data ? data : [];

  const handleShow = () => {
    if (formData.key && formData.date) {
      console.log(formData);
      setCheckData(0);
      changeState(formData);
    } else {
      changeState(formData);
      setCheckData(1);
    }
  };

  const SetFormData = (newData) => {
    console.log(newData);
    let newFormData = formData;
    if (newData.key === "null") {
      newFormData.key = null;
    } else {
      newData.key
        ? (newFormData.key = newData.key)
        : (newFormData.date = newData.date);
    }
    setFormData(newFormData);
  };

  return (
    <div className={classes.root}>
      <Paper variant="outlined">
        <Grid container>
          <Grid item lg={3} md={4} sm={12} xs={12}>
            <Paper elevation={0} className={classes.paper}>
              <Typography component="div" noWrap>
                Sports
              </Typography>
              <Select
                data={sportData}
                changeFormData={(newData) => SetFormData(newData)}
              />
            </Paper>
          </Grid>
          <Grid item lg={2} md={3} sm={12} xs={12}>
            <Paper elevation={0} className={classes.paper}>
              <Typography component="div" noWrap>
                Date
              </Typography>
              <DatePicker
                fullWidth={1}
                changeFormData={(newData) => SetFormData(newData)}
              />
            </Paper>
          </Grid>
          <Grid item lg={2} md={3} sm={12} xs={12}>
            <Paper elevation={0} className={classes.paper}>
              <Typography component="div" noWrap>
                .
              </Typography>
              <Button
                fullWidth
                size="large"
                variant="outlined"
                className={classes.button}
                onClick={handleShow}
              >
                SHOW AVAIL
              </Button>
              {checkData ? (
                <Typography
                  align="center"
                  color="secondary"
                  component="div"
                  noWrap
                >
                  Please select both
                </Typography>
              ) : null}
            </Paper>
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
}
