import React from "react";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import InfoOutlinedIcon from "@material-ui/icons/InfoOutlined";
import Divider from "@material-ui/core/Divider";
import Button from "@material-ui/core/Button";

import Select from "../../common/Select";
import CourtButton from "../../common/CourtButton";
import DatePicker from "../../common/DatePicker";
import { Input } from "@material-ui/core";
// import WeekCheckBox from "../../common/WeekCheckBox";

const useStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: theme.spacing(3),
  },
  paper: {
    padding: theme.spacing(1.5),
    backgroundColor: "#F5F5F5",
    borderColor: "#fff",
    display: "flex",
  },
  typo1: {
    color: "#484848",
    verticalAlign: "middle",
    paddingLeft: theme.spacing(1),
    fontSize: "1rem",
    fontWeight: "bold",
  },
  typo2: {
    color: "#272727",
    paddingTop: theme.spacing(1.5),
    paddingBottom: theme.spacing(1.5),
    fontWeight: "bold",
  },
  typo3: {
    color: "#272727",
    paddingBottom: theme.spacing(0.5),
    fontWeight: "bold",
  },
  icon: {
    paddingTop: "0px",
  },
  div: {
    width: "100%",
  },
  divider: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  button: {
    borderRadius: 50,
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
    marginTop: theme.spacing(4),
    marginBottom: theme.spacing(5),
    backgroundColor: theme.palette.base1.main,
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base1.dark,
    },
  },
}));

export default function Default(props) {
  const { sportData, vendor_complex_id, changeState } = props;
  const classes = useStyles();
  const [formData, setFormData] = React.useState({
    complex_playground_id: vendor_complex_id,
  });

  const SetFormData = (newData, key) => {
    console.log("newData, key :>> ", newData, key);
    setFormData({
      ...formData,
      [key]: newData.key ? newData.key : newData.date,
    });
  };

  const onClick = () => {
    changeState(formData);
  };

  return (
    <Paper className={classes.root} variant="outlined">
      <div className={classes.div}>
        <Paper className={classes.paper} variant="outlined">
          <InfoOutlinedIcon className={classes.icon} />
          <Typography className={classes.typo1}>SEE ALL BULK BOOKING</Typography>
        </Paper>
        <Typography variant="h6" className={classes.typo2}>
          ACTIVITIES
        </Typography>
        <Typography className={classes.typo3} component="div" noWrap>
          Sports
        </Typography>
        <Select data={sportData} regularSelect={1} changeFormData={(newData) => SetFormData(newData, "sports_id")} />
        <Divider className={classes.divider} />
        <Typography variant="h6" className={classes.typo2}>
          COURT
        </Typography>
        <CourtButton getSelectedID={(id) => SetFormData({key: id}, "complex_playground_id")} />
        <Divider className={classes.divider} />
        <Typography variant="h6" className={classes.typo2}>
          DATE
        </Typography>
        <Typography className={classes.typo3} component="div" noWrap>
          From
        </Typography>
        <DatePicker fullWidth={0} changeFormData={(newData) => SetFormData(newData, "from_date")} />
        <Typography className={classes.typo3} component="div" noWrap>
          To
        </Typography>
        <DatePicker fullWidth={0} changeFormData={(newData) => SetFormData(newData, "to_date")} />
        <Divider className={classes.divider} />
        <Typography variant="h6" className={classes.typo2}>
          FROM TIME
        </Typography>
        <Input
          type="time"
          id="from_time"
          variant="outlined"
          onChange={(event) => {
            SetFormData({key: event.target.value}, "from_time");
          }}
        />
        {/* <Select data={from_time} regularSelect={1} changeFormData={(newData) => SetFormData(newData, "from_time")} /> */}
        <Typography variant="h6" className={classes.typo2}>
          TO TIME
        </Typography>
        <Input
          type="time"
          id="to_time"
          variant="outlined"
          onChange={(event) => {
            SetFormData({key: event.target.value}, "to_time");
          }}
        />
        {/* <Select data={to_time} regularSelect={1} changeFormData={(newData) => SetFormData(newData, "to_time")} /> */}
      </div>
      <Button onClick={() => onClick()} className={classes.button} variant="contained">
        SHOW AVAILABILITY
      </Button>
    </Paper>
  );
}
