import React from "react";
import { useHistory } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import { commonGetRequest, bookingAddRequest } from "../../redux/features/Auth";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import { Typography } from "@material-ui/core";
import Divider from "@material-ui/core/Divider";
import classNames from "classnames";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";

import Select from "../common/Select";
import AutoComplete from "../common/Input/SearchInputSelect";
import { memberUrl } from "../../assets/helper/constants";

const numberData = [
  { id: 1, name: "Cash" },
  { id: 2, name: "Card" },
];

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(3),
  },
  divider: {
    marginLeft: "50%",
    color: "#CCCCCC",
  },
  divider2: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    color: "#CCCCCC",
  },
  typo1: {
    color: "#484848",
    fontWeight: "bold",
  },
  typo2: {
    color: "#484848",
    fontWeight: "500",
  },
  typo3: {
    paddingTop: theme.spacing(3),
    color: "#484848",
    fontWeight: "400",
  },
  textAlignR: {
    textAlign: "right",
  },
  paddingLeft: {
    paddingLeft: theme.spacing(3),
  },
  grid1: {
    display: "flex",
    paddingTop: theme.spacing(2),
  },
  grid2: {
    marginLeft: "auto",
  },
  grid3: {
    marginRight: "auto",
  },
  grid4: {
    paddingTop: theme.spacing(2),
  },
  textField: {
    width: "100%",
  },
  button: {
    backgroundColor: "#272727",
    color: "#fff",
    width: "50%",
    marginLeft: "50%",
  },
  button1: {
    backgroundColor: "#272727",
    color: "#fff",
    marginLeft: theme.spacing(1),
  },
  button2: {
    marginRight: theme.spacing(1),
    padding: theme.spacing(1),
    borderRadius: 50,
    minWidth: "150px",
    borderColor: "#040F33",
    textTransform: "none",
  },
  button3: {
    marginLeft: theme.spacing(1),
    padding: theme.spacing(1),
    borderRadius: 50,
    minWidth: "150px",
    backgroundColor: "#41BEAE",
    textTransform: "none",
    color: "#fff",
  },
}));

const SetFormData = (data) => {
  console.log(data);
};

const Checkout = (props) => {
  const history = useHistory();
  const classes = useStyles();

  const { members_list, booking_confirm } = useSelector((state) => state.auth);
  console.log("booking_confirm :>> ", booking_confirm);

  const [form_data, set_form_data] = React.useState({
    booking_type_id: booking_confirm && booking_confirm.bulk ? 13 : 12,
    game_type_id: 4,
    player_limit: 12,
    payment_mode: "cash",
    payment_id: 0,
    payment_signature: "digitally_signed",
  });
  const [members_data, set_members_data] = React.useState([]);
  const [selected_member, set_selected_member] = React.useState(null);
  const [searchField, setSearchField] = React.useState(null);

  const fetchMembersList = (query) => {
    dispatch(
      commonGetRequest({
        url: memberUrl,
        key: "members_list",
        key_name: "user",
        query: query ? "?mobile=" + query : "",
      })
    );
  };

  const addNewMember = () => {
    const win = window.open("/members/add", "_blank");
    win.focus();
  };

  const handleSearch = () => {
    if (searchField) {
      fetchMembersList(searchField);
    }
  };

  const handleResetSearch = () => {
    fetchMembersList();
    set_selected_member(null);
  };

  const dispatch = useDispatch();

  const handleOnChange = (member_info) => {
    set_selected_member(member_info);
    let players = [];
    players.push({ user_id: member_info.id });
    set_form_data({ ...form_data, players });
  };

  React.useEffect(() => {
    const query = new URLSearchParams(props.location.search);
    const unique_id = query.get("unique_id");
    if (booking_confirm.unique_id === unique_id) {
      fetchMembersList();
      set_form_data({
        ...form_data,
        complex_playground_id: booking_confirm.complex_playground_id,
        sports_id: booking_confirm.sports_id,
        playground_timeslot_id: booking_confirm.id,
      });
    } else {
      history.push("/slots");
    }
  }, []);

  React.useEffect(() => {
    if (members_list) {
      const temp_list = members_list.map((ele) => {
        return { id: ele.id, name: ele.name.name };
      });
      set_members_data(temp_list);
    }
  }, [members_list]);

  const handleSubmit = () => {
    dispatch(bookingAddRequest({ form_data }));
  };

  // React.useEffect(() => {
  // // Run it to fetch unique member data
  //   if (selected_member) {
  //     console.log("selected_member :>> ", selected_member);
  //   }
  // }, [selected_member]);

  return (
    <Paper className={classes.paper}>
      <Grid>
        <Grid container lg={12}>
          <Grid lg={4} sm={12}>
            <Typography variant="h6">TURF BOOKING</Typography>
            <Grid className={classes.grid1}>
              <Grid>
                <Typography className={classes.typo2}>{"Football 5-aslide"}</Typography>
                <Typography className={classes.typo2}>
                  {booking_confirm.day_of_week + " " + booking_confirm.date}
                </Typography>
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classNames(classes.typo2, classes.textAlignR)}>
                  Turf 2
                </Typography>
                <Typography className={classes.typo2}>
                  {booking_confirm.from_time + " to " + booking_confirm.to_time}
                </Typography>
              </Grid>
            </Grid>
            <Divider className={classes.divider2} />
            <Typography variant="h6">CUSTOMER DETAILS</Typography>
            <Grid>
              <Typography className={classes.typo3}>CONTACT NUMBER</Typography>
            </Grid>
            <Grid className={classes.grid1}>
              {" "}
              <TextField
                onChange={(event) => setSearchField(event.target.value)}
                className={classes.textField}
                id="search_number"
                label="Search customer number"
                defaultValue=""
                variant="outlined"
                size="small"
                type="input"
              />
              <Button onClick={handleSearch} className={classes.button1} variant="contained">
                search
              </Button>
              <Button onClick={handleResetSearch} className={classes.button1} variant="contained">
                reset
              </Button>
            </Grid>
            <Grid className={classes.grid1}>
              <AutoComplete
                data={members_data}
                handleOnChange={(member_info) => handleOnChange(member_info)}
              />
            </Grid>
            <Grid>
              <Button onClick={addNewMember} className={classes.button} variant="contained">
                add new member
              </Button>
            </Grid>
          </Grid>
          <Grid lg={1} sm={0}>
            <Divider className={classes.divider} orientation="vertical" />
          </Grid>
          <Grid className={classes.paddingLeft} lg={4} sm={12}>
            <Typography variant="h6">BOOKING DETAILS</Typography>
            <Grid className={classes.grid1}>
              <Grid>
                <Typography className={classes.typo2}>Turf Price</Typography>
                <Typography className={classes.typo2}>Extras</Typography>
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classNames(classes.typo2, classes.textAlignR)}>
                  Rs {booking_confirm.total_amount}
                </Typography>
                <Typography className={classNames(classes.typo2, classes.textAlignR)}>
                  Rs 0.00
                </Typography>
              </Grid>
            </Grid>
            <Divider className={classes.divider2} />
            <Grid className={classes.grid1}>
              <Grid>
                <Typography className={classes.typo2}>Net Price</Typography>
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classNames(classes.typo2, classes.textAlignR)}>
                  Rs {booking_confirm.total_amount}
                </Typography>
              </Grid>
            </Grid>
            <Divider className={classes.divider2} />
            <Typography variant="h6">PAYMENT DETAILS</Typography>
            <Grid className={classes.grid1}>
              <Grid lg={3}>
                <Typography className={classes.typo2}>Advance</Typography>
              </Grid>

              <Grid lg={6}>
                <TextField
                  className={classes.textField}
                  id="advance"
                  label="Rs 0"
                  defaultValue=""
                  variant="outlined"
                  size="small"
                  type="input"
                />
              </Grid>

              <Grid lg={3}>
                {" "}
                <Select
                  data={numberData}
                  regularSelect={1}
                  changeFormData={(newData) => SetFormData(newData)}
                />
              </Grid>
            </Grid>
            <Divider className={classes.divider2} />
            <Grid className={classes.grid1}>
              <Grid>
                <Typography className={classes.typo2}>
                  <b>Total Price</b>
                </Typography>
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classNames(classes.typo2, classes.textAlignR)}>
                  <b>Rs {booking_confirm.total_amount}</b>
                </Typography>
              </Grid>
            </Grid>
            <Grid className={classes.grid1}>
              <Grid className={classes.grid2}>
                <Button className={classes.button2} variant="outlined">
                  Dismiss
                </Button>
              </Grid>
              <Grid className={classes.grid3}>
                <Button onClick={handleSubmit} className={classes.button3} variant="contained">
                  Book Now
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Paper>
  );
};

export default Checkout;
