import React from "react";
import Table from "../../common/Table2";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";

import { useHistory } from "react-router";

import { useDispatch, useSelector } from "react-redux";
import { commonGetRequest, setSnackBar, setIsLoading } from "../../../redux/features/Auth";

import Server from "../../../Server";

import {
    offerList,
    generateSnackData,
    offerDelete,
} from "../../../assets/helper/constants";

const useStyles = makeStyles((theme) => ({
    typo3: {
        color: "#464646",
        textAlign: "left",
        fontSize: "15px",
        fontWeight: "400",
    },
}));

const columns = [
    { id: "sport_id", label: "Sport Id", minWidth: 100 },
    { id: "name", label: "Coupon Name", minWidth: 120 },
    { id: "date", label: "Dates/Days", minWidth: 100 },
    {
        id: "timing",
        label: "Timing",
        minWidth: 120,
        align: "center",
    },
    {
        id: "discount",
        label: "Discount",
        minWidth: 120,
    },
    {
        id: "action",
        label: "action",
        minWidth: 120,
    },
];

const List = (props) => {
    const classes = useStyles();

    const { couponData, isLoading } = useSelector(
        (state) => state.auth
    );
    const dispatch = useDispatch();

    React.useEffect(() => {
        dispatch(
            commonGetRequest({
                url: offerList,
                key: "couponData",
                query: props.searchVal ? "?search=" + props.searchVal : ""
            })
        );
    }, [dispatch, props.searchVal]);

    const styleId = (id) => {
        return (
            <Typography variant="h5" className={classes.typo1}>
                {id}
            </Typography>
        );
    };

    const styleOthers = (value, bold) => {
        return <Typography className={bold ? classes.typo3 : classes.typo2}>{value}</Typography>;
    };

    const deleteRow = async (id) => {
        if (window.confirm("Are you sure you want to delete this?")) {
            dispatch(setIsLoading());
            try {
                let res = await Server.get(offerDelete + "/" + id);

                if (res.data && res.data.status) {
                    dispatch(setSnackBar(generateSnackData(true, res.data.message, "success")));
                } else {
                    dispatch(setSnackBar(generateSnackData(true, res.data.message, "error")));
                }
                dispatch(setIsLoading());
                dispatch(
                    commonGetRequest({
                        url: offerList,
                        key: "couponData",
                        query: props.searchVal ? "?search=" + props.searchVal : ""
                    })
                );
            } catch (error) {
                const resp =
                    error.response && error.response.data.message
                        ? error.response.data.message
                        : "Couldn't connect to our servers.";

                dispatch(setSnackBar(generateSnackData(true, resp, "error")));
                dispatch(setIsLoading());
            }
        }
    }

    const history = useHistory();
    const editRow = (id) => {
        history.push("/offers/add", { id: id })
    }

    const styleAction = (id) => {
        return (
            <div>
                <IconButton onClick={() => deleteRow(id)} className={classes.icon} aria-label="create pricing" component="span">
                    <DeleteOutlineIcon fontSize="medium" />
                </IconButton>
                <IconButton onClick={() => editRow(id)} className={classes.icon} aria-label="create pricing" component="span">
                    <EditOutlinedIcon fontSize="medium" />
                </IconButton>
            </div>
        );
    };

    function createData(
        oldid,
        oldname,
        oldphone,
        oldemail,
        oldduration,
        oldaction
    ) {
        const sport_id = styleOthers(oldid);
        const name = styleOthers(oldname);
        const date = styleOthers(oldphone);
        const timing = styleOthers(oldemail);
        const discount = styleOthers(oldduration);
        const action = styleAction(oldaction);
        return { sport_id, name, date, timing, discount, action };
    }

    let rows = [];

    if (!isLoading && couponData) {
        couponData.map(function (offData) {
            rows.push(
                createData(
                    offData.sport_id,
                    offData.name,
                    offData.from_date + " to " + offData.to_date + " :: " + offData.days,
                    offData.from_time + " to " + offData.to_time,
                    offData.discount_percent ? offData.discount_percent + " %" : offData.amount + " Rs",
                    offData.id,
                )
            )
            return true;
        })
    }

    // const rows = [
    //   createData(
    //     "Badminton",
    //     "Diwali Offer",
    //     "1-Jan to 31-Jan_21::All Days",
    //     "4pm-6pm",
    //     "Percentage 5%::Limit: 1000",
    //     "Amit Shah::05-Sep-21 | 06:30 am",
    //     "1"
    //   ),
    // ];
    return <Table columns={columns} rows={rows} />;
};

export default List;
