import React from "react";
import { Form, Field } from "react-final-form";
import { useDispatch } from "react-redux";
import { step3AddOrUpdate } from "../../redux/features/Auth";

import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import CloseIcon from "@material-ui/icons/Close";
import Slide from "@material-ui/core/Slide";
import { Grid } from "@material-ui/core";

import Input from "../common/Input/LoginOutlinedInput";

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: "relative",
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
  typo1: {
    fontSize: "19px",
    textTransform: "uppercase",
    fontWeight: 700,
  },
  typo2: {
    fontSize: "15px",
    fontWeight: 500,
  },
  grid1: {
    padding: theme.spacing(3),
  },
  grid2: {
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "30%",
    },
  },
  grid3: {
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "80%",
    },
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function FullScreenDialog(props) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { open, handleClose, complex_data, update_data } = props;
  let [globalForm, setGlobalForm] = React.useState({});
  let formData = new FormData();

  const prefillData = () => {
    if (complex_data && complex_data.complex_id) {
      if (update_data) {
        setGlobalForm({ name: update_data.name, id: update_data.id });
      }
      formData.append("sports_complex_id", complex_data.complex_id);
    }
  };

  React.useEffect(() => {
    prefillData();
  }, [update_data]);

  const required = (value) => (value ? undefined : "* Required");

  const name = ({ input, meta }) => {
    return <Input id="name" label="Enter Name" input={input} meta={meta} />;
  };

  const image = ({ input, meta }) => {
    return (
      <Input
        id="image"
        type="file"
        file={true}
        input={input}
        meta={meta}
        setFile={(file) => {
          console.log(file);
          formData.append("image", file);
        }}
      />
    );
  };

  const onSubmit = async (values) => {
    for (const key in values) {
      if (key != "image") {
        formData.append(key, values[key]);
      }
    }
    formData.append("sports_complex_id", complex_data.complex_id);
    for (var pair of formData.entries()) {
      console.log(pair[0] + ", " + pair[1]);
    }
    if (update_data) {
      dispatch(
        step3AddOrUpdate({
          add: 0,
          data: formData,
        })
      );
      handleClose();
    } else {
      dispatch(
        step3AddOrUpdate({
          add: 1,
          data: formData,
        })
      );
      handleClose();
    }
  };

  return (
    <Form
      initialValues={globalForm}
      onSubmit={onSubmit}
      render={({ handleSubmit, submitting, values }) => (
        <form onSubmit={handleSubmit}>
          <div>
            <Dialog
              fullScreen
              open={open}
              onClose={handleClose}
              TransitionComponent={Transition}
            >
              <AppBar className={classes.appBar}>
                <Toolbar>
                  <IconButton
                    edge="start"
                    color="inherit"
                    onClick={handleClose}
                    aria-label="close"
                  >
                    <CloseIcon />
                  </IconButton>
                  <Typography variant="h6" className={classes.title}>
                    Add/Edit Amenities
                  </Typography>
                  <Button autoFocus color="inherit" onClick={handleClose}>
                    close
                  </Button>
                </Toolbar>
              </AppBar>
              <Grid
                container
                justifyContent="center"
                alignItems="center"
                className={classes.grid1}
              >
                <Grid className={classes.grid2}>
                  <Typography className={classes.typo3}>Name</Typography>
                  <Field
                    name="name"
                    component={name}
                    placeholder="Enter Name"
                    validate={required}
                  />
                </Grid>
              </Grid>
              <Grid
                container
                justifyContent="center"
                alignItems="center"
                className={classes.grid1}
              >
                <Grid className={classes.grid2}>
                  <Typography className={classes.typo3}>Image</Typography>
                  <Field
                    name="image"
                    component={image}
                    placeholder="Address Line 2"
                  />
                </Grid>
              </Grid>
              <Grid
                container
                justifyContent="center"
                alignItems="middle"
                className={classes.grid1}
              >
                <Grid className={classes.grid2}>
                  <Button
                    autoFocus
                    color="primary"
                    variant="contained"
                    onClick={() => onSubmit(values)}
                    disabled={submitting ? submitting : false}
                  >
                    save{" "}
                  </Button>
                </Grid>
              </Grid>
            </Dialog>
          </div>
        </form>
      )}
    />
  );
}
