import React from "react";
import { useSelector } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import { Paper, Typography } from "@material-ui/core";
import { Grid } from "@material-ui/core";

import LinearProgress from "../common/LinearProgress";

import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step4 from "./Step4";

const useStyles = makeStyles((theme) => ({
  paper: {
    backgroundColor: `#fff`,
    padding: theme.spacing(3),
  },
  typo1: {
    fontSize: "19px",
    textTransform: "uppercase",
    fontWeight: 700,
  },
  typo2: {
    paddingTop: theme.spacing(1),
    fontSize: "15px",
    marginBottom: theme.spacing(3),
    fontWeight: 500,
    [theme.breakpoints.up("lg")]: {
      width: "42%",
    },
  },
  grid1: {
    marginBottom: theme.spacing(1),
  },
}));

const Onboard = () => {
  const { steps } = useSelector((state) => state.auth);
  const classes = useStyles();

  const handleSwitchCase = (cases) => {
    switch (cases) {
      case 0:
        return <Step1 />;
      case 1:
        return <Step2 />;
      case 2:
        return <Step3 />;
      case 3:
        return <Step4 />;
      default:
        return "Something went wrong!";
    }
  };

  return (
    <>
      <Paper className={classes.paper}>
        <Grid>
          <LinearProgress progress={((steps + 1) / 4) * 100 - 10} />
          <Grid
            className={classes.grid1}
            container
            direction="columns"
            justifyContent="space-between"
          >
            <Grid>
              <Typography variant="h4">Step {steps + 1}</Typography>
            </Grid>
            <Grid>
              <Typography variant="h4">Step 4</Typography>
            </Grid>
          </Grid>
          <Typography className={classes.typo1}>Hello There!</Typography>
          <Typography className={classes.typo2}>
            Before we proceed, we would require you to fill out a few details
            about your organisation that will help the users on our app while
            booking a session with you!{" "}
          </Typography>
          {handleSwitchCase(steps)}
        </Grid>
      </Paper>
    </>
  );
};

export default Onboard;
