import React from "react";
import { BrowserRouter, Route, Redirect, Switch } from "react-router-dom";
import "react-calendar/dist/Calendar.css";
import { makeStyles } from "@material-ui/core/styles";

// Views
import Drawer from "../layouts/Drawer";
import NotFound from "../layouts/NotFound";

// Schedule
import Onboard from "../components/auth/Onboard";
import VendorList from "../components/vendor/list/Index";
import VendorAdd from "../components/vendor/add/Index";

const useStyles = makeStyles((theme) => ({
  toolbar: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    [theme.breakpoints.up("sm")]: {
      marginLeft: "280px",
      width: "calc(100% - 280px)",
    },
    padding: theme.spacing(0),
  },
}));

const App = () => {
  const classes = useStyles();
  return (
    <BrowserRouter>
      <Drawer admin={true} />
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <Switch>
          <Route path="/pagenotfound" exact render={(props) => <NotFound {...props} />} />
          <Route path="/onboarding_form" exact render={(props) => <Onboard {...props} />} />
          <Route path="/vendor-list" exact render={(props) => <VendorList {...props} />} />
          <Route path="/vendor/add" exact render={(props) => <VendorAdd {...props} />} />
        </Switch>
      </main>
    </BrowserRouter>
  );
};

export default App;
