import React from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(0.5),
    borderRadius: 50,
    textTransform: "none",
    padding: theme.spacing(1.2),
    minWidth: "15%",
    backgroundColor: theme.palette.base2.main,
    marginRight: theme.spacing(2),
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base2.dark,
    },
  },
  button2: {
    margin: theme.spacing(0.5),
    borderRadius: 50,
    textTransform: "none",
    padding: theme.spacing(1.2),
    minWidth: "15%",
    borderColor: theme.palette.base2.main,
    marginRight: theme.spacing(2),
    color: "#272727",
    "&:hover": {
      borderColor: theme.palette.base2.dark,
    },
  },
}));

const CourtButton = (props) => {
  const { turfData, getSelectedID, selectedTurfId } = props;
  const classes = useStyles();
  const [id, changeId] = React.useState(selectedTurfId);

  console.log('turfData :>> ', turfData);

  const handleChange = (id) => {
    changeId(id);
    getSelectedID(id);
  };

  return turfData.map((turf) => {
    return (
      <Button
        className={selectedTurfId === turf.id ? classes.button : classes.button2}
        id={id}
        variant={selectedTurfId === turf.id ? "contained" : "outlined"}
        onClick={() => handleChange(turf.id)}
      >
        {turf.name}
      </Button>
    );
  });
};

export default CourtButton;
