import "date-fns";
import React from "react";
import DateFnsUtils from "@date-io/date-fns";
import { MuiPickersUtilsProvider, KeyboardDatePicker } from "@material-ui/pickers";

export default function MaterialUIPickers(props) {
  // The first commit of Material-UI
  const { changeFormData, fullWidth } = props;
  const [selectedDate, setSelectedDate] = React.useState(new Date(props.value ? props.value : new Date()));

  const handleDateChange = (date) => {
    setSelectedDate(date);
    changeFormData({date: date})
  };

  return (
    <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <KeyboardDatePicker
        size="small"
        id="date-picker-dialog"
        format="dd/MM/yyyy"
        style = {{width: fullWidth ? '100%' : '30%'}}
        value={selectedDate}
        inputVariant="outlined"
        onChange={handleDateChange}
        KeyboardButtonProps={{
          "aria-label": "change date",
        }}
        name={props.name ? props.name : "date"}
      />
    </MuiPickersUtilsProvider>
  );
}
