import React from "react";
import { useHistory } from "react-router";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import { Paper } from "@material-ui/core";
import ControlPointIcon from "@material-ui/icons/ControlPoint";
import IconButton from "@material-ui/core/IconButton";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import CardContent from "@material-ui/core/CardContent";
import Card from "@material-ui/core/Card";

import Modal from "../common/Modal";
import Server from "../../Server";
import { pricingListUrl } from "../../assets/helper/constants";

const useStyles = makeStyles((theme) => ({
  right: {
    marginLeft: "auto",
  },
  grid: {
    padding: theme.spacing(3),
    paddingBottom: "5px !important",
    width: "60%",
    [theme.breakpoints.down("md")]: {
      width: "100%",
    },
  },
  card: {
    minWidth: "100%",
  },
  cardContent: {
    display: "flex",
  },
  typo1: {
    fontSize: "15px",
    color: "#272727",
    fontWeight: "600",
    display: "inline",
  },
  typo2: {
    color: "#808080",
  },
  icon: {
    color: "#272727",
  },
}));

const PricingList = ( props ) => {
  const { data = [], deletePricingW } = props;

  console.log(props, "props");
  
  const classes = useStyles();
  const history = useHistory();
  const editPricing = (id) => {
      history.push("/pricing/add", { id: id })
  }
  const [open, setOpen] = React.useState(false);
  const [pricingDeleteId, setPricingDeleteId] = React.useState([]);

  const AddPricing = () => {
    history.push("/pricing/add");
  };

  const DeletePricing = (id) => {
    console.log('true');
    setOpen(true);
    setPricingDeleteId(id);
  };

  const setFunctionOpen = (state) => {
    setOpen(false);
    if(state)
      deletePricingW(pricingDeleteId);
  };

  // const PriceType = () => {
  //   return pricingList;
  // };

  return (
    <div>
      <Modal open={open} setFunctionOpen={(state) => setFunctionOpen(state)} title="Delete Pricing" description="You want to delete Regular Pricing?"/>
      <Paper>
        <Grid className={classes.grid} container alignItems="center">
          <Grid>
            <Typography variant="h6" component="h6">
              REGULAR
            </Typography>
          </Grid>
          <Grid className={classes.right}>
            <IconButton
              onClick={AddPricing}
              className={classes.icon}
              aria-label="create pricing"
              component="span"
            >
              <ControlPointIcon />
            </IconButton>
            <Typography className={classes.typo1}>ADD PRICING</Typography>
          </Grid>
        </Grid>
        <Grid className={classes.grid} container alignItems="center">
          {/* <PriceType /> */}
          {data.length > 0 ? data.map(element => { return (
                      <Card className={classes.card} variant="outlined" key={`pricing_${element.id}`}>
                      <CardContent className={classes.cardContent}>
                        <Grid>
                          <Typography variant="h6" component="h6">
                            {element.from_day} - {element.to_day}
                          </Typography>
                        </Grid>
                        <Grid className={classes.right}>
                          <IconButton
                            onClick={() => DeletePricing(element?.id)}
                            className={classes.icon}
                            aria-label="Delete Pricing"
                            component="span"
                          >
                            <DeleteOutlineIcon />
                          </IconButton>
                          <IconButton className={classes.icon} aria-label="Delete Pricing" component="span">
                            <EditOutlinedIcon onClick={() => editPricing(element?.id)}/>
                          </IconButton>
                        </Grid>
                      </CardContent>
                      <CardContent className={classes.cardContent}>
                        <Grid>
                          <Typography className={classes.typo2} variant="h6" component="h6">
                            {element.start_time} - {element.end_time}
                          </Typography>
                        </Grid>
                        <Grid className={classes.right}>
                          <Typography className={classes.typo2} variant="h6" component="h6">
                            Rs {element.slot_extension_pricing}
                          </Typography>
                        </Grid>
                      </CardContent>
                      </Card>)
          }) : <h3>No data found</h3>}
        </Grid>
      </Paper>
    </div>
  );
};

export default PricingList;
