import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import { InputLabel } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  formControl: {
    width: "100%",
    marginTop: theme.spacing(1),
  },
  select: {
    borderRadius: 50,
  },
}));

export default function SimpleSelect(props) {
  const { id, data, label, input, meta, key_name, handleNext } = props;
  const [newData, setNewData] = React.useState([]);
  const classes = useStyles();

  React.useEffect(() => {
    const temp = data
      ? data.map((ele) => {
          return {
            id: ele.id,
            name: ele[key_name],
          };
        })
      : [];
    setNewData(temp);
  }, []);

  return (
    <div>
      <FormControl
        size="small"
        variant="outlined"
        className={classes.formControl}
      >
        <InputLabel htmlFor={id}>{label}</InputLabel>
        <Select
          label={label}
          className={classes.select}
          id={id}
          value={input.value ? input.value : "none"}
          onChange={(event, value) => {
            input.onChange(event.target.value);
            handleNext(event.target.value, id);
          }}
          error={meta && meta.touched && meta.error ? true : false}
        >
          <MenuItem key="none" disabled value="none">
            <em>None</em>
          </MenuItem>
          {newData.map((element, i) => {
            return (
              <MenuItem key={element.name + i} value={element.id}>
                {element.name}
              </MenuItem>
            );
          })}
        </Select>
      </FormControl>
    </div>
  );
}
