import React from "react";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";
import "../assets/css/Index.css";
import { isAuth } from "../assets/helper/auth";
import PrivateRoutes from "./private";
import AdminRoutes from "./adminPrivate";
import PublicRoutes from "./public";

const PrivateComponents = () => {
  return isAuth() && (isAuth().role_id === 2) ? (
    <Route
      path="/"
      render={(state) => {
        return <PrivateRoutes />;
      }}
    />
  ) : isAuth().role_id === 1 ? (
    <Route
      path="/"
      render={(state) => {
        return <AdminRoutes />;
      }}
    />
  ) : (
    <Redirect path="/login" />
  );
};

function AllRoutes() {
  return (
    <BrowserRouter>
      <Switch>
        {PublicRoutes.length &&
          PublicRoutes.map((route) => (
            <Route
              key={route.path}
              exact
              path={route.path}
              component={route.component}
            />
          ))}
        <PrivateComponents />
      </Switch>
    </BrowserRouter>
  );
}

export default AllRoutes;
