import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import { Button } from "@material-ui/core";

import PopperComponent from "./Popper";

const useStyles = makeStyles((theme) => ({
  root: {
    [theme.breakpoints.up("lg")]: {
      maxWidth: "50%",
    },
  },
  container: {
    maxHeight: "70vh",
  },
}));

export default function StickyHeadTable(props) {
  const { data } = props;
  // console.log("data :>> ", data);
  const [columns, setColumns] = React.useState([]);
  const [rows, setRows] = React.useState([]);
  // console.log("columns :>> ", columns);
  // console.log("rows :>> ", rows);
  const classes = useStyles();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };

  function createData(ele) {
    let tempData = {};
    tempData.time =
      ele.day_of_week + "\nFrom: " + ele.from_time + "\nTo: " + ele.to_time;
    data.map((newEle) => {
      tempData[newEle.playground_name] = createButton(ele);
    });
    return tempData;
  }

  const createButton = (data) => {
    return data ? (
      <PopperComponent data={data} />
    ) : (
      <Button fullWidth disabled variant="outlined">
        IXYWONTP
      </Button>
    );
  };

  React.useEffect(() => {
    if (data) {
      let finalData = [
        { id: "time", label: "Time", align: "left", minWidth: 200 },
      ];
      data.map((ele, i) => {
        finalData.push({
          id: ele.playground_name,
          key: ele.id + i,
          label: ele.playground_name,
          align: "left",
          minWidth: 200,
        });
      });

      const tempRows =
        data && data[0] && data[0].timeSlots
          ? data[0].timeSlots.map((ele, i) => {
              return createData(ele);
            })
          : [];
      setColumns(finalData);
      setRows(tempRows);
    }
  }, [data]);

  return (
    <Paper className={classes.root}>
      <TableContainer className={classes.container}>
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              {columns.map((column) => (
                <TableCell
                  key={column.id}
                  align={column.align}
                  style={{ minWidth: column.minWidth }}
                >
                  {column.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows
              .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              .map((row) => {
                return (
                  <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                    {columns.map((column) => {
                      const value = row[column.id];
                      return (
                        <TableCell key={column.id} align={column.align}>
                          {column.format && typeof value === "number"
                            ? column.format(value)
                            : value}
                        </TableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[10, 25, 100]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Paper>
  );
}
