import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";

const useStyles = makeStyles(() => ({
  formControl: {
    width: "100%",
  },
  formControl2: {
    width: "60%",
  },
  select: {
    borderRadius: 50,
  },
}));

export default function SimpleSelect(props) {
  const { data, changeFormData, regularSelect } = props;
  const classes = useStyles();
  const [value, setValue] = React.useState("none");

  const handleChange = (event) => {
    const value = event.target.value ? event.target.value : "none";
    setValue(value);
    changeFormData({ key: value });
  };

  return (
    <div>
      <FormControl
        size="small"
        variant="outlined"
        className={regularSelect ? classes.formControl2 : classes.formControl}
      >
        <Select
          className={regularSelect ? null : classes.select}
          id="simple-select-outlined"
          onChange={handleChange}
          defaultValue={props.defaultValue ? props.defaultValue : value}
        >
          {props.hideNone ? null : (
            <MenuItem value={"none"}>
              <em>None</em>
            </MenuItem>
          )}

          {data.map((element) => {
            return <MenuItem value={element.id}>{element.name}</MenuItem>;
          })}
        </Select>
      </FormControl>
    </div>
  );
}
