import React from "react";
import { useDispatch, useSelector } from "react-redux";

import Header from "./Header";
import Default from "./Default";
import List from "./List";

import { commonGetRequest } from "../../../redux/features/Auth";
import {
  sportsListUrl,
  getPlaygroundSlots,
} from "../../../assets/helper/constants";

export default function Slots() {
  const dispatch = useDispatch();
  const { sports_list, slots_list } = useSelector((state) => state.auth);
  const [stage, setStage] = React.useState(0);
  const [userInput, setUserInput] = React.useState({});

  const loadInitialValues = () => {
    dispatch(
      commonGetRequest({
        url: sportsListUrl,
        key: "sports_list",
      })
    );
  };

  React.useEffect(() => {
    loadInitialValues();
  }, []);

  const changeState = (data) => {
    if (data.key && data.date && data.confirm) {
      setUserInput(data);
      setStage(2);
    } else if (data.key && data.date) {
      dispatch(
        commonGetRequest({
          url: getPlaygroundSlots,
          key: "slots_list",
          query:
            "?sports_id=" +
            data.key +
            "&date=" +
            data.date.getFullYear() +
            "-" +
            String(data.date.getMonth() + 1).padStart(2, "0") +
            "-" +
            String(data.date.getDate()).padStart(2, "0"),
        })
      );
      setUserInput(data);
      setStage(1);
    } else {
      setUserInput({});
      setStage(0);
    }
  };

  const SwitchComponent = () => {
    switch (stage) {
      case 0:
        return <Default />;
      case 1:
        return (
          <List
            userInput={userInput}
            data={
              slots_list && slots_list[0] && slots_list[0].playgrounds
                ? slots_list[0].playgrounds
                : []
            }
          />
        );
      default:
        return <Default />;
    }
  };

  return (
    <>
      <Header changeState={(data) => changeState(data)} data={sports_list} />
      <SwitchComponent />
    </>
  );
}
