import React from "react";
import Header from "./Header";
import Default from "./Default";
import List from "./List";

import { useSelector, useDispatch } from "react-redux";
import { commonGetRequest } from "../../redux/features/Auth";
import { setSnackBar, setIsLoading } from "../../redux/features/Auth";
import { sportsListUrl, complexListUrl, pricingListUrl, generateSnackData, deletePriceUrl } from "../../assets/helper/constants";

import Server from "../../Server";

const Index = () => {
  const { sports_list, complex_list, isLoading } = useSelector(
    (state) => state.auth
  );
  const [price_data, setPriceData] = React.useState([]);
  const [sports_id, setSportsId] = React.useState(0);
  const [vendor_complex_id, setComplexId] = React.useState(0);
  // console.log(price_data);
  const dispatch = useDispatch();
  React.useEffect(() => {
    dispatch(
      commonGetRequest({
        url: sportsListUrl,
        key: "sports_list",
      })
    );
    dispatch(
      commonGetRequest({
        url: complexListUrl,
        key: "complex_list",
        key_name: "playground_name",
      })
    );
  }, []);

  const deletePricing = async (id) => {
    console.log(id);
    try {
      let res = await Server.get(deletePriceUrl + '/' + id);

      if (res.data && res.data.status) {
        dispatch(setSnackBar(generateSnackData(true, res.data.message, "success")));
        handleSelect({sports: 1, turf: 1})
      } else {
        dispatch(setSnackBar(generateSnackData(true, res.data.message, "error")));
      }
    } catch (er) {
      console.log(er);
      dispatch(setSnackBar(generateSnackData(true, "Something went wrong.", "error")));
    }
  };

  const handleSelect = async (e) => {
    setComplexId(e.turf);
    setSportsId(e.sports);
    dispatch(setIsLoading());
    // dispatch(
    //   commonGetRequest({
    //     url: pricingListUrl,
    //     key: "price_data",
    //     query: `?sports_id=${e.sports}&vendor_complex_id=${e.turf}`
    //   })
    // );
    try {
      let res = await Server.get(pricingListUrl+`?sports_id=${e.sports}&vendor_complex_id=${e.turf}`);

      if (res.data && res.data.status) {
        setPriceData(res.data.result);
      } else {
        setPriceData([]);
      }
      dispatch(setIsLoading());
    } catch (error) {
      const resp =
        error.response && error.response.data.message
          ? error.response.data.message
          : "Couldn't connect to our servers.";

      dispatch(setSnackBar(generateSnackData(true, resp, "error")));
      dispatch(setIsLoading());
    }
    console.log(price_data, "price_data");
  };

  return (
    <div>
      <Header
        sports_list={sports_list}
        complex_list={complex_list}
        handleSelect={(e) => handleSelect(e)}
      />
      {
        (sports_id && vendor_complex_id) ? <List data={price_data} deletePricingW={(id) => deletePricing(id)} /> : <Default />
      }
    </div>
  );
};

export default Index;
