import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import { isAuth } from "../../../assets/helper/auth";
import { commonGetRequest, commonPostRequest, setSnackBar } from "../../../redux/features/Auth";
import { sportsListUrl, generateSnackData, generateISODate, getSlotsAvailUrl } from "../../../assets/helper/constants";

import LHS from "./LHS";
import RHS from "./RHS";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    [theme.breakpoints.down("sm")]: {
      display: "block",
    },
  },
}));

export default function Slots() {
  const dispatch = useDispatch();
  const classes = useStyles();
  const { sports_list, slots_bulk_list } = useSelector((state) => state.auth);
  const [formData, setFormData] = React.useState([]);
  const [complexId] = React.useState(isAuth().vendor_complex_id);

  console.log("slots_bulk_list :>> ", slots_bulk_list);

  const loadInitialValues = () => {
    dispatch(
      commonGetRequest({
        url: sportsListUrl,
        key: "sports_list",
      })
    );
  };

  React.useEffect(() => {
    loadInitialValues();
  }, []);

  const changeState = (data) => {
    if (Object.keys(data).length === 6) {
      let formData = {
        ...data,
        from_date: generateISODate(data.from_date),
        to_date: generateISODate(data.to_date),
      };
      setFormData(data);
      dispatch(
        commonPostRequest({
          url: getSlotsAvailUrl,
          key: "slots_bulk_list",
          formData: formData,
        })
      );
    } else {
      dispatch(setSnackBar(generateSnackData(true, "Please select all fields.", "error")));
    }
  };

  return (
    <>
      <Grid container lg={12} className={classes.root}>
        <Grid item lg={8} sm={12}>
          <LHS sportData={sports_list} vendor_complex_id={complexId} changeState={(data) => changeState(data)} />
        </Grid>
        <Grid item lg={4} sm={12}>
          {/* {formData.from_date && formData.to_date ? ( */}
          <RHS formData={formData} />
          {/* ) : null} */}
        </Grid>
      </Grid>
    </>
  );
}
