import React, { Fragment, useState } from "react";
import { Paper } from "@material-ui/core";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import FormControl from "@material-ui/core/FormControl";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Radio from "@material-ui/core/Radio";
import Button from "@material-ui/core/Button";

import { useHistory } from "react-router-dom";
import { Form, Field } from "react-final-form";
import Input from "../../common/Input/LoginOutlinedInput";

import Server from "../../../Server";
import { useDispatch, useSelector } from "react-redux";
import { setSnackBar, setIsLoading } from "../../../redux/features/Auth";
import { commonGetRequest } from "../../../redux/features/Auth";

import {
    vendorAdd,
    vendorEdit,
    vendorGet,
    generateSnackData
} from "../../../assets/helper/constants"

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(3),
    },
    typo1: {
        color: "#484848",
        fontSize: "16px",
        fontWeight: "600",
    },
    typo2: {
        color: "#464646",
        fontSize: "20px",
    },
    textField: {
        width: "95%",
        marginTop: theme.spacing(1),
    },
    grid: {
        marginTop: theme.spacing(2),
    },
    grid2: {
        marginTop: theme.spacing(0.5),
        display: "flex",
        marginRight: theme.spacing(3),
    },
    grid3: {
        marginLeft: "auto",
    },
    divider: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
    },
    button: {
        borderRadius: 50,
        marginTop: theme.spacing(2),
        backgroundColor: theme.palette.base1.main,
        color: "#fff",
        "&:hover": {
            backgroundColor: theme.palette.base1.dark,
        }
    }
}));

const Forms = () => {
    const dispatch = useDispatch();
    const history = useHistory();

    const [gender, setGender] = useState("male");
    const { isLoading, vendor_data } = useSelector(
        (state) => state.auth
    );

    let [globalForm, setGlobalForm] = React.useState(null);

    React.useEffect(() => {
        if (history.location.state) {
            dispatch(
                commonGetRequest({
                    url: vendorGet,
                    key: "vendor_data",
                    query: "?id=" + history.location.state.id
                })
            );
        }
    }, [dispatch, history.location.state])

    React.useEffect(() => {
        if (vendor_data) {
            setGlobalForm({
                id: vendor_data.id,
                name: vendor_data.name,
                mobile: vendor_data.login.mobile,
                email: vendor_data.login.email,
                gender: vendor_data.gender,
            });
        }
    }, [vendor_data])

    const required = (value) => (value ? undefined : "* Required");

    const classes = useStyles();

    const name_ = ({ input, meta }) => {
        return (
            <Input
                id="name"
                label="Enter vendor name"
                input={input}
                meta={meta}
            />
        );
    };

    const mobile_ = ({ input, meta }) => {
        return (
            <Input
                id="mobile"
                label="Enter vendor mobile"
                input={input}
                meta={meta}
            />
        );
    };

    const email_ = ({ input, meta }) => {
        return (
            <Input
                id="email"
                label="Enter vendor email"
                input={input}
                meta={meta}
            />
        );
    };

    const password_ = ({ input, meta }) => {
        return (
            <Input
                id="password"
                label="Enter vendor password"
                input={input}
                meta={meta}
                type="password"
            />
        );
    };


    const onSubmit = async (values) => {
        dispatch(setIsLoading());
        setGlobalForm(values);
        values.gender = gender;
        try {
            let res = [];
            if (globalForm && globalForm.id) {
                res = await Server.put(vendorEdit, values)
            } else {
                res = await Server.post(vendorAdd, values)
            }

            if (res.data && res.data.status) {
                dispatch(setSnackBar(generateSnackData(true, res.data.message, "success")));
                history.push("/vendor-list");
            } else {
                dispatch(setSnackBar(generateSnackData(true, res.data.message, "error")));
            }
            dispatch(setIsLoading());
        } catch (error) {
            const resp =
                error.response && error.response.data.message
                    ? error.response.data.message
                    : "Couldn't connect to our servers.";

            dispatch(setSnackBar(generateSnackData(true, resp, "error")));
            dispatch(setIsLoading());
        }
    }

    return (
        <Paper className={classes.paper}>
            {
                !isLoading
                    ?
                    <Form
                        initialValues={globalForm}
                        onSubmit={onSubmit}
                        render={({ handleSubmit, submitting, values }) => (
                            <form onSubmit={handleSubmit}>
                                <Grid className={classes.grid} container lg={8}>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>VENDOR NAME</Typography>
                                        <Field
                                            className={classes.textField}
                                            variant="outlined"
                                            size="small"
                                            name="name"
                                            component={name_}
                                            validate={required}
                                        />
                                    </Grid>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>PHONE NUMBER</Typography>
                                        <Field
                                            className={classes.textField}
                                            variant="outlined"
                                            size="small"
                                            name="mobile"
                                            component={mobile_}
                                            validate={required}
                                        />
                                    </Grid>
                                </Grid>
                                <Grid className={classes.grid} container lg={8}>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>EMAIL ID</Typography>
                                        <Field
                                            className={classes.textField}
                                            variant="outlined"
                                            size="small"
                                            name="email"
                                            component={email_}
                                            validate={required}
                                        />
                                    </Grid>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>PASSWORD</Typography>
                                        <Field
                                            className={classes.textField}
                                            variant="outlined"
                                            size="small"
                                            name="password"
                                            component={password_}
                                            validate={globalForm ? null : required}
                                        />
                                    </Grid>
                                </Grid>
                                <Grid className={classes.grid} container lg={8}>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>GENDER</Typography>
                                        <FormControl
                                            className={classes.textField}
                                            required
                                            component="fieldset"
                                        >
                                            <RadioGroup
                                                aria-label="gender"
                                                defaultValue={(gender) ? gender : 'female'}
                                                name="radio-buttons-group"
                                            >
                                                <FormControlLabel
                                                    control={<Radio onClick={(e) => setGender(e.target.value)} name="gender" value="male" />}
                                                    label="Male"
                                                />
                                                <FormControlLabel
                                                    control={<Radio onClick={(e) => setGender(e.target.value)} name="gender" value="female" />}
                                                    label="Female"
                                                />
                                                <FormControlLabel
                                                    control={
                                                        <Radio onClick={(e) => setGender(e.target.value)} name="gender" value="other" />
                                                    }
                                                    label="Other"
                                                />
                                            </RadioGroup>
                                        </FormControl>
                                    </Grid>
                                </Grid>
                                <Button type="submit" disabled={submitting ? submitting : false} className={classes.button} variant="contained">
                                    {
                                        globalForm
                                        ?
                                        "UPDATE VENDOR"
                                        :
                                        "CREATE VENDOR"
                                    }
                                </Button>
                            </form>
                        )
                        }
                    />
                    :
                    null
            }
        </Paper >
    );
};

export default Forms;
