import React from "react";
import { useHistory } from "react-router";

import { CardContent } from "@material-ui/core";
import { Card } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";

import DatePicker from "../common/DatePicker";
import Select from "../common/Select";

import { useDispatch, useSelector } from "react-redux";
import { commonGetRequest, setSnackBar, setIsLoading } from "../../redux/features/Auth";

import Server from "../../Server";

import List from './List';

import {
    reportUrl,
    generateSnackData
} from "../../assets/helper/constants";

function convertDate(inputFormat) {
    function pad(s) { return (s < 10) ? '0' + s : s; }
    var d = new Date(inputFormat)
    return [d.getFullYear(), pad(d.getMonth() + 1), pad(d.getDate())].join('-')
}

const useStyles = makeStyles((theme) => ({
    card: {
        padding: theme.spacing(3),
        backgroundColor: "#FCFCFC",
    },
    typo3: {
        color: "#272727",
        paddingBottom: theme.spacing(0.5),
        fontWeight: "bold",
    },
    button: {
        marginTop: theme.spacing(2),
        marginRight: theme.spacing(2),
        borderRadius: 50,
        backgroundColor: theme.palette.base1.main,
        color: "#fff",
        "&:hover": {
            backgroundColor: theme.palette.base1.dark,
        },
    },
}));

const transaction = [
    {
        id: "all",
        name: "All",
    },
    {
        id: 34,
        name: "Pending Payment",
    },
    {
        id: 35,
        name: "Only Paid",
    },
    {
        id: 36,
        name: "Only Cancelled",
    },
];

const Index = (props) => {
    const [formData, setFormData] = React.useState({
        from_date: convertDate(new Date()),
        to_date: convertDate(new Date()),
        state_id: 'all',
    });
    const classes = useStyles();

    const { reportsData, isLoading } = useSelector(
        (state) => state.auth
    );
    const dispatch = useDispatch();

    const viewReport = () => {
        dispatch(
            commonGetRequest({
                url: reportUrl,
                key: "reportsData",
                query: `?from_date=${formData.from_date}&to_date=${formData.to_date}&state_id=${formData.state_id === 'all' ? 0 : formData.state_id}`
            })
        );
    };

    const downloadReport = async () => {
        dispatch(setIsLoading());
        try {
            const FileDownload = require('js-file-download');
            let res = await Server.get(reportUrl + `?from_date=${formData.from_date}&to_date=${formData.to_date}&state_id=${formData.state_id === 'all' ? 0 : formData.state_id}&export=1`, { responseType: "blob" })

            if (res.data) {
                FileDownload(res.data, `${formData.from_date}-to-${formData.to_date}.csv`);
                dispatch(setSnackBar(generateSnackData(true, res.data.message, "success")));
                // history.push("/members");
            } else {
                dispatch(setSnackBar(generateSnackData(true, res.data.message, "error")));
            }
            dispatch(setIsLoading());
        } catch (error) {
            const resp =
                error.response && error.response.data.message
                    ? error.response.data.message
                    : "Couldn't connect to our servers.";

            dispatch(setSnackBar(generateSnackData(true, resp, "error")));
            dispatch(setIsLoading());
        }
    }

    return (
        <Grid>
            <Card className={classes.card} variant="outlined">
                <CardContent>
                    <Grid container lg={12}>
                        <Grid lg={2}>
                            <Typography className={classes.typo3} component="div" noWrap>
                                From
                            </Typography>
                            <DatePicker fullWidth={1} changeFormData={(newData) => setFormData({ ...formData, from_date: convertDate(newData.date) })} />
                        </Grid>
                        <Grid lg={1} />
                        <Grid lg={2}>
                            <Typography className={classes.typo3} component="div" noWrap>
                                To
                            </Typography>
                            <DatePicker fullWidth={1} changeFormData={(newData) => setFormData({ ...formData, to_date: convertDate(newData.date) })} />
                        </Grid>
                        <Grid lg={1} />
                        <Grid lg={3}>
                            <Typography className={classes.typo3} component="div" noWrap>
                                Transaction Type
                            </Typography>
                            <Select
                                hideNone={true}
                                defaultValue={"all"}
                                data={transaction}
                                funct="showt"
                                title="Show Transaction"
                                changeFormData={(newData) => setFormData({ ...formData, state_id: newData.sports })}
                            />
                        </Grid>
                    </Grid>
                    <Button variant="outlined" onClick={downloadReport} className={classes.button}>
                        DOWNLOAD REPORT
                    </Button>
                    <Button onClick={viewReport} variant="outlined" className={classes.button}>
                        VIEW REPORT
                    </Button>
                </CardContent>
            </Card>
            {
                reportsData
                    ?
                    <List reportsData={reportsData} />
                    :
                    null
            }
        </Grid>
    );
};

export default Index;
