import React from "react";

import { bookingList, booking_status } from "../../../assets/helper/constants";
import Table from "../../common/Table2";
import Server from "../../../Server";
import { makeStyles, Typography } from "@material-ui/core";
import { useHistory } from "react-router";

const AllBookingList = () => {
  const useStyles = makeStyles((theme) => ({
    textDanger: {
        color: "red"
    },
    textSucces: {
        color: "green"
    },
    textWarning: {
        color: "orange"
    },
    typo1: {
        color: "#F3555E",
        fontWeight: "600",
    },
    typo2: {
        color: "#808080",
        fontWeight: "500",
        fontSize: "18px",
    },
    typo3: {
        color: "#808080",
        fontWeight: "700",
        fontSize: "18px",
    },
}));
const classes = useStyles();
  const [rows, setRows] = React.useState([]);
  const columns = [
    { id: "bookingNo", label: "Booking No.", minWidth: 100 },
    { id: "playground_name", label: "Ground Name", minWidth: 120 },
    { id: "date", label: "Dates/Days", minWidth: 100 },
    {
      id: "timing",
      label: "Timing",
      minWidth: 120,
      align: "center",
    },
    {
      id: "discount",
      label: "Discount",
      minWidth: 120,
    },
    {
      id: "total_amount",
      label: "Total Amount",
      minWidth: 120,
    },
    {
      id: "status",
      label: "Status",
      minWidth: 120,
    },
    {
        id: "action",
        label: "Action",
    }
  ];

  const celData = (cd) => {
    return <Typography className={`${classes.typo3}`} 
    style={cd === "Pending" ? {color: "orange"} : (cd === "Cancelled" ? {color: "red"} : (cd === "Confirmed" ? {color: "green"} : {}))}>{cd}</Typography>
  }
  const history = useHistory();

  const goToDetail = (id) => {
    history.push("/booking-details", { id: id })
  }

  React.useEffect(async () => {
    let res = await Server.get(bookingList);
    if (res.data && res.data.status) {
        const tempData = res.data.result.map((ele) => {
            const bookingNo = celData(ele.bookingNo + "");
            const playground_name = celData(ele.complex_playground.playground_name);
            const date = celData(ele.booking_detail.playground_timeslot.date);
            const timing = celData(ele.booking_detail.playground_timeslot.from_time + " to " + ele.booking_detail.playground_timeslot.to_time);
            const discount = celData(ele.discount_amount);
            const total_amount = celData(ele.total_amount);
            const status = celData(booking_status[ele.state_id]);
            const action = <a className={classes.typo3} onClick={() => goToDetail(ele.id)}>Details</a>;
            return {bookingNo, playground_name, date, timing, discount, total_amount, status, action};
        });
        setRows(tempData);
    } else {
        setRows([]);
    }
  }, []);

  return (
    <div>
        <Table columns={columns} rows={rows} />
    </div>
  );
};

export default AllBookingList;
