// Functions

export const generateSnackData = (open, message, type) => {
  return { open, message, type };
};

export const generateISODate = (date) => {
  return date.getFullYear() + "-" + String(date.getMonth() + 1).padStart(2, "0") + "-" + String(date.getDate()).padStart(2, "0");
};

