import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { commonGetRequest, step1AddOrUpdate } from "../../redux/features/Auth";
import { Form, Field } from "react-final-form";
import { makeStyles } from "@material-ui/core/styles";
import { Typography } from "@material-ui/core";
import { Grid } from "@material-ui/core";

import Input from "../common/Input/LoginOutlinedInput";
import OutlinedSelect from "../common/Input/OutlinedSelectOnboard";
import Submit from "../common/Button/BlueContainedRounded";
import Modal from "../common/Map/FullScreenModal";

import { isAuth } from "../../assets/helper/auth";

import {
  countryUrl,
  stateUrl,
  cityUrl,
  uniqueVendorComplex,
} from "../../assets/helper/constants";

const useStyles = makeStyles((theme) => ({
  typo1: {
    fontSize: "19px",
    textTransform: "uppercase",
    fontWeight: 700,
  },
  typo2: {
    paddingTop: theme.spacing(1),
    fontSize: "15px",
    fontWeight: 500,
    [theme.breakpoints.up("lg")]: {
      width: "42%",
    },
  },
  typo3: {
    textTransform: "uppercase",
    fontSize: "16px",
    fontWeight: 700,
  },
  grid1: {
    paddingTop: theme.spacing(5),
  },
  grid2: {
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "30%",
      paddingRight: theme.spacing(4),
    },
  },
  grid3: {
    width: "30%",
  },
  grid4: {
    paddingTop: theme.spacing(0.8),
  },
  paddingTop: {
    paddingTop: theme.spacing(2),
  },
}));

const Step1 = () => {
  const { country_data, state_data, city_data, complex_data } = useSelector(
    (state) => state.auth
  );
  let [globalForm, setGlobalForm] = React.useState([]);
  let form = new FormData();
  const classes = useStyles();
  const dispatch = useDispatch();

  React.useEffect(() => {
    dispatch(
      commonGetRequest({
        url: countryUrl,
        key: "country_data",
      })
    );
    dispatch(
      commonGetRequest({
        url: uniqueVendorComplex,
        key: "complex_data",
      })
    );
  }, []);

  React.useEffect(() => {
    if (complex_data.state_id) {
      dispatch(
        commonGetRequest({
          url: stateUrl,
          key: "state_data",
          query: complex_data.country_id,
        })
      );
      dispatch(
        commonGetRequest({
          url: cityUrl,
          key: "city_data",
          query: complex_data.state_id,
        })
      );
      setGlobalForm({ ...complex_data, user_id: isAuth().user_id });
    }
  }, [complex_data]);

  const setLocation = (value, id) => {
    if (id === "country_id") {
      dispatch(
        commonGetRequest({
          url: stateUrl,
          key: "state_data",
          query: value,
        })
      );
    } else if (id === "state_id") {
      dispatch(
        commonGetRequest({
          url: cityUrl,
          key: "city_data",
          query: value,
        })
      );
    }
  };

  const required = (value) => (value ? undefined : "* Required");

  const country_id = ({ input, meta }) => {
    return (
      <OutlinedSelect
        handleNext={(value, id) => setLocation(value, id)}
        id="country_id"
        data={country_data}
        label="Select Country"
        key_name="country_name"
        input={input}
        meta={meta}
      />
    );
  };

  const state_id = ({ input, meta }) => {
    return (
      <OutlinedSelect
        handleNext={(value, id) => setLocation(value, id)}
        id="state_id"
        data={state_data}
        label="Select State"
        key_name="state_name"
        input={input}
        meta={meta}
      />
    );
  };

  const city_id = ({ input, meta }) => {
    return (
      <OutlinedSelect
        handleNext={(value, id) => setLocation(value, id)}
        id="city_id"
        data={city_data}
        label="Select City"
        key_name="city_name"
        input={input}
        meta={meta}
      />
    );
  };

  const contact = ({ input, meta }) => {
    return (
      <Input
        id="contact"
        label="Enter Contact Number"
        input={input}
        meta={meta}
      />
    );
  };

  const lat = ({ input, meta }) => {
    return <Input id="lat" label="Latitude" input={input} meta={meta} />;
  };

  const lang = ({ input, meta }) => {
    return <Input id="lang" label="Longitude" input={input} meta={meta} />;
  };

  const complex_name = ({ input, meta }) => {
    return (
      <Input
        id="complex_name"
        label="Enter Complex Name"
        input={input}
        meta={meta}
      />
    );
  };

  const landmark = ({ input, meta }) => {
    return (
      <Input id="landmark" label="Enter Landmark" input={input} meta={meta} />
    );
  };

  const line_1 = ({ input, meta }) => {
    return (
      <Input
        id="line_1"
        label="Address Line 1"
        multiline={true}
        rows={4}
        input={input}
        meta={meta}
      />
    );
  };

  const line_2 = ({ input, meta }) => {
    return (
      <Input
        id="line_2"
        label="Address Line 2"
        multiline={true}
        rows={4}
        input={input}
        meta={meta}
      />
    );
  };

  const min_price = ({ input, meta }) => {
    return (
      <Input id="min_price" label="Minimum Price" input={input} meta={meta} />
    );
  };

  const image = ({ input, meta }) => {
    return (
      <Input
        id="image"
        type="file"
        file={true}
        input={input}
        meta={meta}
        setFile={(file) => {
          form.append("image", file);
          console.log(form);
        }}
      />
    );
  };

  const onSubmit = async (values) => {
    for (const key in values) {
      if (key !== "image") form.append(key, values[key]);
    }
    if (complex_data.complex_id) {
      form.append("id", complex_data.complex_id);
      dispatch(
        step1AddOrUpdate({
          add: 0,
          data: form,
        })
      );
    } else {
      dispatch(
        step1AddOrUpdate({
          add: 1,
          data: form,
        })
      );
    }
  };

  const setCoords = (position, values) => {
    setGlobalForm({ ...values, lat: position[0], lang: position[1] });
  };

  return (
    <>
      <Form
        initialValues={globalForm}
        onSubmit={onSubmit}
        render={({ handleSubmit, submitting, values }) => (
          <form onSubmit={handleSubmit}>
            <Grid container className={classes.grid1}>
              <Grid className={classes.grid2}>
                <Typography className={classes.typo3}>
                  Select Country
                </Typography>
                <Field
                  name="country_id"
                  component={country_id}
                  placeholder="Select Country"
                  validate={required}
                />
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classes.typo3}>Select State</Typography>
                <Field
                  name="state_id"
                  component={state_id}
                  placeholder="Select State"
                  validate={required}
                />
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classes.typo3}>Select City</Typography>
                <Field
                  name="city_id"
                  component={city_id}
                  placeholder="Select City"
                  validate={required}
                />
              </Grid>
            </Grid>
            <Grid container className={classes.grid1}>
              <Grid className={classes.grid2}>
                <Typography className={classes.typo3}>complex name</Typography>
                <Field
                  name="complex_name"
                  component={complex_name}
                  placeholder="Complex Name"
                  validate={required}
                />
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classes.typo3}>landmark</Typography>
                <Field
                  name="landmark"
                  component={landmark}
                  placeholder="Landmark"
                  validate={required}
                />
              </Grid>
            </Grid>
            <Grid container className={classes.grid1}>
              <Grid className={classes.grid2}>
                <Typography className={classes.typo3}>
                  contact number
                </Typography>
                <Field
                  name="contact"
                  component={contact}
                  placeholder="Enter contact number"
                  validate={required}
                />
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classes.typo3}>
                  venue location pin
                </Typography>
                <Grid container>
                  <Grid className={classes.grid4}>
                    <Modal
                      getCoords={(position) => setCoords(position, values)}
                    />
                  </Grid>
                  <Grid className={classes.grid3}>
                    {" "}
                    <Field
                      name="lat"
                      component={lat}
                      placeholder="Latitude"
                      validate={required}
                    />
                  </Grid>
                  <Grid className={classes.grid3}>
                    {" "}
                    <Field
                      name="lang"
                      component={lang}
                      placeholder="Latitude"
                      validate={required}
                    />
                  </Grid>
                </Grid>
              </Grid>
              <Grid container className={classes.grid1}>
                <Grid className={classes.grid2}>
                  <Typography className={classes.typo3}>
                    Venue Address Line 1
                  </Typography>
                  <Field
                    name="line_1"
                    component={line_1}
                    placeholder="Venue Line 1"
                    validate={required}
                  />
                </Grid>
                <Grid className={classes.grid2}>
                  <Typography className={classes.typo3}>
                    Venue Address Line 2
                  </Typography>
                  <Field
                    name="line_2"
                    component={line_2}
                    placeholder="Address Line 2"
                    validate={required}
                  />
                </Grid>
              </Grid>
              <Grid container className={classes.grid1}>
                <Grid className={classes.grid2}>
                  <Typography className={classes.typo3}>
                    Minimum Booking Price
                  </Typography>
                  <Field
                    name="min_price"
                    component={min_price}
                    placeholder="Minimum Price"
                    validate={required}
                  />
                </Grid>
                <Grid className={classes.grid2}>
                  <Typography className={classes.typo3}>Venue Image</Typography>
                  <Field
                    name="image"
                    component={image}
                    placeholder="Address Line 2"
                  />
                </Grid>
              </Grid>
              <Grid
                className={classes.paddingTop}
                container
                direction="row"
                justifyContent="flex-start"
                alignItems="center"
              >
                <Submit
                  title="back"
                  type="submit"
                  width="110px"
                  disabled={true}
                />
                <Submit
                  title="next"
                  type="submit"
                  width="110px"
                  disabled={submitting ? submitting : false}
                />
              </Grid>
            </Grid>
          </form>
        )}
      />
    </>
  );
};

export default Step1;
