import React from "react";
import { Paper } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Table from "../common/Table2";

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(3),
    },
    button: {
        marginTop: theme.spacing(3),
        marginRight: theme.spacing(2),
        borderRadius: 50,
        backgroundColor: theme.palette.base1.main,
        color: "#fff",
        "&:hover": {
            backgroundColor: theme.palette.base1.dark,
        },
    },
    card: {
        borderColor: "#F3555E",
        marginTop: theme.spacing(2),
    },
    typo1: {
        color: "#272727",
        textAlign: "center",
        fontWeight: "900",
    },
    typo2: {
        color: "#808080",
        textAlign: "center",
    },
    typo3: {
        color: "#464646",
        textAlign: "left",
        fontSize: "15px",
    },
}));

function convertDate(inputFormat) {
    function pad(s) { return (s < 10) ? '0' + s : s; }
    var d = new Date(inputFormat)
    return [d.getFullYear(), pad(d.getMonth() + 1), pad(d.getDate())].join('-')
}


const columns = [
    { id: "timestamp", label: "Timestamp", minWidth: 100 },
    { id: "bookingdetail", label: "Booking Detail", minWidth: 120 },
    { id: "customerdetail", label: "Customer Detail", minWidth: 100 },
    {
        id: "amount",
        label: "Amount/Mode",
        minWidth: 120,
    },
    {
        id: "receipt",
        label: "Receipt",
        minWidth: 120,
    },
    {
        id: "type",
        label: "Type",
        minWidth: 120,
    },
];

const List = (props) => {
    const classes = useStyles();
    const styleOthers = (data) => {
        const i = data.indexOf("::");
        if (i >= 0) {
            return (
                <div>
                    <Typography className={classes.typo3}>{data.substr(0, i)}</Typography>
                    <Typography className={classes.typo3}>{data.substr(i + 2)}</Typography>
                </div>
            );
        } else return <Typography className={classes.typo3}>{data}</Typography>;
    };

    function createData(
        otimestamp,
        obookingdetail,
        ocustomerdetail,
        oamount,
        oreceipt,
        otype,
    ) {
        const timestamp = styleOthers(otimestamp);
        const bookingdetail = styleOthers(obookingdetail);
        const customerdetail = styleOthers(ocustomerdetail);
        const amount = styleOthers(oamount);
        const receipt = styleOthers(oreceipt);
        const type = styleOthers(otype);
        return { timestamp, bookingdetail, customerdetail, amount, receipt, type };
    }

    // const rows = [
    //     createData(
    //         "11-Sep-21 :: 4-00-5:00pm",
    //         "Ref ID: 11733284 :: Football 7-aside",
    //         "Sunny Mail :: 993234553",
    //         "Rs 650 :: Cash",
    //         "29",
    //         "Cancel",
    //         "Admin/Playmaker :: Time booked 05-Sep-21 | 09:30 pm"
    //     ),
    // ];
    let rows = [];

    if (props.reportsData) {
        props.reportsData.map(function (repData) {
            rows.push(
                createData(
                    `${convertDate(repData.created_on)} :: ${repData.booking_detail.playground_timeslot.from_time} - ${repData.booking_detail.playground_timeslot.to_time}`,
                    `Ref Id: ${repData.bookingNo} :: ${repData.complex_playground.playground_name}`,
                    `${repData.user.name} :: ${repData.user.login.mobile}`,
                    `Rs ${repData.total_amount}`,
                    `${repData.id}`,
                    `${repData.state_id === 34 ? "Pending" : repData.state_id === 35 ? "Confirmed" : "Cancelled"}`,
                )
            )
            return true;
        })
    }

    return (
        <Grid>
            <Paper className={classes.paper}>
                <Table columns={columns} rows={rows} bgWhite={1} hidePagination={true} />
            </Paper>
        </Grid>
    );
};

export default List;
