import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { commonGetRequest } from "../../redux/features/Auth";
import { makeStyles } from "@material-ui/core/styles";
import { Typography } from "@material-ui/core";
import { Grid } from "@material-ui/core";
import { Tooltip } from "@material-ui/core";
import IconButton from "@material-ui/core/IconButton";

import AddIcon from "@material-ui/icons/AddCircleOutline";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";

import {
  getAmenitiesUrl,
  serverUrl,
  amenitiesDeleteUrl,
} from "../../assets/helper/constants";
import { setSteps } from "../../redux/features/Auth";

import Submit from "../common/Button/BlueContainedRounded";
import Table from "../common/Table2";

import Step3AddEdit from "./Step3AddEdit";

const useStyles = makeStyles((theme) => ({
  typo1: {
    fontSize: "19px",
    textTransform: "uppercase",
    fontWeight: 700,
  },
  typo2: {
    fontSize: "15px",
    fontWeight: 500,
  },
  grid1: {
    paddingTop: theme.spacing(5),
  },
  grid2: {
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "30%",
      paddingRight: theme.spacing(4),
    },
  },
  grid3: {
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "80%",
    },
  },
  paddingTop: {
    paddingTop: theme.spacing(2),
  },
}));

const Step3 = () => {
  const [compData, setCompData] = React.useState(null);
  const [open, setOpen] = React.useState(false);
  const [rows, setRows] = React.useState([]);
  const { amenities_data, complex_data, steps } = useSelector(
    (state) => state.auth
  );
  const classes = useStyles();
  const dispatch = useDispatch();

  const handleClose = () => {
    setOpen(false);
    setTimeout(() => {
      loadInitialValues();
    }, 800);
  };

  const loadInitialValues = () => {
    dispatch(
      commonGetRequest({
        url: getAmenitiesUrl,
        key: "amenities_data",
        query: complex_data.complex_id ? complex_data.complex_id : null,
      })
    );
  };

  React.useEffect(() => {
    loadInitialValues();
  }, []);

  const styleText = (text) => {
    return <Typography className={classes.typo2}>{text}</Typography>;
  };

  const styleImage = (link) => {
    return (
      <img src={serverUrl + link} alt="image" width="50px" height="auto" />
    );
  };

  const createButton = (data) => {
    return (
      <>
        <Tooltip title="Edit">
          <IconButton
            onClick={() => (setCompData(data), setOpen(true))}
            aria-label="Edit"
            component="span"
          >
            <EditOutlinedIcon fontSize="medium" />
          </IconButton>
        </Tooltip>
        <Tooltip title="Delete">
          <IconButton
            onClick={() => handleDelete(data)}
            aria-label="Delete"
            component="span"
          >
            <DeleteOutlineIcon fontSize="medium" />
          </IconButton>
        </Tooltip>
      </>
    );
  };

  const createData = (osrno, oname, oimage, ele) => {
    const srno = styleText(osrno + 1);
    const name = styleText(oname);
    const image = styleImage(oimage);
    const actions = createButton(ele);
    return { srno, name, image, actions };
  };

  const handleDelete = (data) => {
    console.log('data :>> ', data);
    dispatch(
      commonGetRequest({
        url: amenitiesDeleteUrl,
        key: "delete",
        query: data.id,
      })
    );
    setTimeout(() => {
      loadInitialValues();
    }, 800);
  };

  React.useEffect(() => {
    if (amenities_data) {
      setRows(
        amenities_data.map((ele, i) => {
          return createData(i, ele.name, ele.image, ele);
        })
      );
    }
  }, [amenities_data]);

  const columns = [
    { id: "srno", label: "Sr No.", align: "left", minWidth: 150 },
    { id: "name", label: "Name", align: "center", minWidth: 200 },
    { id: "image", label: "Image", align: "center", minWidth: 200 },
    { id: "actions", label: "Actions", align: "center", minWidth: 200 },
  ];

  return (
    <>
      <Step3AddEdit
        open={open}
        handleClose={handleClose}
        complex_data={complex_data}
        update_data={compData}
      />
      <Grid container className={classes.grid1} justifyContent="space-between">
        <Grid className={classes.grid2}>
          <Typography className={classes.typo1}>Amenities</Typography>
        </Grid>
        <Grid className={classes.grid2}>
          <Tooltip title="Add">
            <IconButton
              onClick={() => (setCompData(null), setOpen(true))}
              aria-label="Add"
              component="span"
            >
              <AddIcon fontSize="medium" />
            </IconButton>
          </Tooltip>
        </Grid>
      </Grid>
      <Grid container className={classes.grid1}>
        <Grid className={classes.grid3}>
          <Table
            columns={columns ? columns : []}
            rows={rows ? rows : []}
          ></Table>
        </Grid>
      </Grid>
      <Grid
        className={classes.paddingTop}
        container
        direction="row"
        justifyContent="flex-start"
        alignItems="center"
      >
        <Submit
          handleclick={() => dispatch(setSteps(steps - 1))}
          title="back"
          width="110px"
        />
        <Submit
          handleclick={() => dispatch(setSteps(steps + 1))}
          title="next"
          width="110px"
        />
      </Grid>
    </>
  );
};

export default Step3;
