import React from "react";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";

import { useHistory } from "react-router-dom";

import { Form, Field } from "react-final-form";
import OutlinedSelect from "../../../common/Input/OutlinedSelect";
import Input from "../../../common/Input/LoginOutlinedInput";

import Server from "../../../../Server";
import { useDispatch, useSelector } from "react-redux";
import { setSnackBar, setIsLoading } from "../../../../redux/features/Auth";
import { commonGetRequest } from "../../../../redux/features/Auth";

import {
  memberShipPackageAddUrl,
  memberShipPackageEditUrl,
  memberShipPackageUniqueUrl,
  getPolicyUrl,
  generateSnackData
} from "../../../../assets/helper/constants"

const useStyles = makeStyles((theme) => ({
  grid: {
    padding: theme.spacing(3),
  },
  typo1: {
    marginTop: theme.spacing(1.5),
    color: "#484848",
    fontSize: "16px",
    fontWeight: "600",
  },
  textField: {
    width: "95%",
    marginTop: theme.spacing(1),
  },
  button: {
    borderRadius: 50,
    marginTop: theme.spacing(2),
    backgroundColor: theme.palette.base1.main,
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base1.dark,
    },
  },
}));

const Forms = (props) => {
  const dispatch = useDispatch();
  const history = useHistory();

  const { policy_data, isLoading, membership_package } = useSelector(
    (state) => state.auth
  );

  let [globalForm, setGlobalForm] = React.useState({});
  const classes = useStyles();

  React.useEffect(() => {
    dispatch(
      commonGetRequest({
        url: getPolicyUrl,
        key: "policy_data",
        query: "1"
      })
    );
  }, [dispatch]);

  React.useEffect(() => {
    if (history.location.state) {
      dispatch(
        commonGetRequest({
          url: memberShipPackageUniqueUrl,
          key: "membership_package",
          query: "/" + history.location.state.id
        })
      );
    }
  }, [dispatch, history.location.state])

  React.useEffect(() => {
    setGlobalForm(membership_package);
  }, [membership_package])

  const required = (value) => (value ? undefined : "* Required");

  const package_name = ({ input, meta }) => {
    return (
      <Input
        id="package_name"
        label="Enter Package Name"
        input={input}
        meta={meta}
      />
    );
  };

  const package_price_ = ({ input, meta }) => {
    return (
      <Input
        id="amount"
        label="Enter Package Price"
        input={input}
        meta={meta}
      />
    );
  };

  const package_duration_ = ({ input, meta }) => {
    return (
      <OutlinedSelect
        id="duration"
        key_name="duration"
        label="Select Package Duration"
        input={input}
        meta={meta}
        handleNext={(value, id) => console.log(value, id)}
        returnValue={(value) => console.log(value, input)}
        data={[{ id: '3 months', name: '3 months' }, { id: 'Monthly package', name: 'Monthly package' }, { id: 'Yearly package', name: 'Yearly package' }]}
      />
    );
  };

  const policy_ = ({ input, meta }) => {
    let policyData = [];
    policy_data.map(function (polData) {
      policyData.push({ id: polData.id, name: polData.policy_text })
      return false;
    })
    return (
      <OutlinedSelect
        id="policy_id"
        key_name="policy_id"
        label="Select Policy"
        input={input}
        meta={meta}
        handleNext={(value, id) => console.log(value, id)}
        returnValue={(value) => console.log(value, input)}
        data={policyData}
      />
    );
  };

  const onSubmit = async (values) => {
    dispatch(setIsLoading());
    try {
      let res = await Server.post(globalForm && globalForm.id ? memberShipPackageEditUrl : memberShipPackageAddUrl, values);

      if (res.data && res.data.status) {
        dispatch(setSnackBar(generateSnackData(true, res.data.message, "success")));
      } else {
        dispatch(setSnackBar(generateSnackData(true, res.data.message, "error")));
      }
      dispatch(setIsLoading());
      history.push("/members/packages");
    } catch (error) {
      const resp =
        error.response && error.response.data.message
          ? error.response.data.message
          : "Couldn't connect to our servers.";

      dispatch(setSnackBar(generateSnackData(true, resp, "error")));
      dispatch(setIsLoading());
    }
  }

  return (
    <>
      {
        !isLoading
          ?
          <Form
            initialValues={globalForm}
            onSubmit={onSubmit}
            render={({ handleSubmit, submitting, values }) => (
              <form onSubmit={handleSubmit}>
                {
                  globalForm
                    ?
                    <input type="hidden" name="id" value={globalForm.id} />
                    :
                    null
                }
                <Grid className={classes.grid}>
                  <Grid lg={4}>
                    <Typography className={classes.typo1}>PACKAGE NAME</Typography>
                    <Field
                      className={classes.textField}
                      name="package_name"
                      component={package_name}
                      validate={required}
                    />
                  </Grid>
                  <Grid lg={4}>
                    <Typography className={classes.typo1}>PACKAGE DURATION</Typography>
                    <Field
                      className={classes.textField}
                      name="duration"
                      component={package_duration_}
                      validate={required}
                    />
                  </Grid>
                  <Grid lg={4}>
                    <Typography className={classes.typo1}>PACKAGE PRICING</Typography>
                    <Field
                      className={classes.textField}
                      name="amount"
                      component={package_price_}
                      validate={required}
                    />
                  </Grid>
                  <Grid lg={4}>
                    <Typography className={classes.typo1}>Select Policy</Typography>
                    <Field
                      className={classes.textField}
                      name="policy_id"
                      component={policy_}
                      validate={required}
                    />

                  </Grid>
                  <Grid>
                    <Button type="submit" className={classes.button} disabled={submitting ? submitting : false}>
                      CONFIRM PACKAGE
                    </Button>
                  </Grid>
                </Grid>
              </form>
            )}
          />
          :
          null
      }
    </>
  );
};

export default Forms;
