import { createTheme } from "@material-ui/core/styles";

export default function DarkTheme() {
  const theme = createTheme({
    palette: {
      base1: {
        main: "#040F33",
        dark: "#020a23",
        light: "#363f5b",
      },
      base2: {
        main: "#F3555E",
        dark: "#aa3b41",
        light: "#f5777e",
      },
      info: {
        main: "#41BEAE",
      },
      type: "light",
    },
    typography: {
      fontFamily: ["Barlow"].join(","),
    },
  });

  return theme;
}
