export const commonGetRequest = createAsyncThunk("auth/commonGetRequest", async (payload, { rejectWithValue, dispatch }) => {
  try {
    const { url, key, query, key_name } = payload;
    let data = query ? await Server.get(url + query, payload) : await Server.get(url, payload);
    data.key = key;
    data.key_name = key_name ? key_name : null;
    return data;
  } catch (error) {
    const resp = error.response ? error.response.data.message : "Couldn't connect to our servers.";
    dispatch(setSnackBar(generateSnackData(true, resp, "error")));
    rejectWithValue(error);
  }
});

export const commonPostRequest = createAsyncThunk("auth/commonPostRequest", async (payload, { rejectWithValue, dispatch }) => {
  try {
    const { url, key, formData } = payload;
    let data = await Server.post(url, formData, payload);
    data.key = key;
    return data;
  } catch (error) {
    const resp = error.response ? error.response.data.message : "Couldn't connect to our servers.";
    dispatch(setSnackBar(generateSnackData(true, resp, "error")));
    rejectWithValue(error);
  }
});

export const bookingAddRequest = createAsyncThunk("auth/bookingAddRequest", async (payload, { rejectWithValue, dispatch }) => {
  try {
    const { form_data } = payload;
    let data = await Server.post(bookingAdd, form_data);
    if (data && data.data && data.data.status === 1) {
      data = await Server.post(bookingPayment, {
        ...form_data,
        order_id: data.data.result.id,
      });
      if (data && data.data && data.data.status === 1) {
        dispatch(setSnackBar(generateSnackData(true, data.data.message, "success")));
      }
      console.log("data2 :>> ", data);
      console.log("coming here");
    }
    return data;
  } catch (error) {
    const resp = error.response ? error.response.data.message : "Couldn't connect to our servers.";
    dispatch(setSnackBar(generateSnackData(true, resp, "error")));
    rejectWithValue(error);
  }
});

const initialState = {
  isLoading: false,
  snackBar: {
    open: false,
    message: "",
    type: "",
  },
  token: null,
  onBoardVendorForm: null,
  steps: 0,
  country_data: [],
  state_data: [],
  city_data: [],
  complex_data: {},
  sub_types: [],
  policy_data: [],
  sports_list: [],
  complex_list: [],
  price_data: [],
  amenities_data: [],
  slots_list: [],
  slots_bulk_list: [],
  offer_data: [],
  members_list: [],
  booking_confirm: [],
  flush: [],
  playgrounds_list: [],
};

const authSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    setSnackBar: (state, action) => {
      state.snackBar = action.payload;
    },
    setIsLoading: (state, action) => {
      state.isLoading = !state.isLoading;
    },
    setOnboardVendorForm: (state, action) => {
      state.onBoardVendorForm = action.payload;
    },
    setSteps: (state, action) => {
      state.steps = action.payload;
    },
    setBookingConfirm: (state, action) => {
      state.booking_confirm = action.payload;
    },
  },
  extraReducers: {
    [loginSubmitThunk.pending]: (state, action) => {
      state.isLoading = true;
    },
    [loginSubmitThunk.fulfilled]: (state, action) => {
      let data = action.payload;
      if (data && data.data && data.data.result) {
        const { name, role_id, token, user_id, profile_update, vendor_complex_id } = data.data.result;
        const user = {
          vendor_complex_id,
          user_id,
          role_id,
          name,
          profile_update,
        };
        authenticate(user, token, () => {});
        state.token = token;
      }
      state.isLoading = false;
    },
    [loginSubmitThunk.rejected]: (state, action) => {
      state.isLoading = false;
    },
    [step1AddOrUpdate.pending]: (state, action) => {
      state.isLoading = true;
    },
    [step1AddOrUpdate.fulfilled]: (state, action) => {
      let data = action.payload;
      if (data && data.data && data.data.message) {
        state.snackBar = generateSnackData(true, data.data.message, "success");
        state.steps = 1;
      }
      state.isLoading = false;
    },
    [step1AddOrUpdate.rejected]: (state, action) => {
      state.isLoading = false;
    },
    [step2AddOrUpdate.pending]: (state, action) => {
      state.isLoading = true;
    },
    [step2AddOrUpdate.fulfilled]: (state, action) => {
      let data = action.payload;
      if (data && data.data && data.data.message) {
        state.snackBar = generateSnackData(true, data.data.message, "success");
      }
      state.isLoading = false;
    },
    [step2AddOrUpdate.rejected]: (state, action) => {
      state.isLoading = false;
    },
    [step3AddOrUpdate.pending]: (state, action) => {
      state.isLoading = true;
    },
    [step3AddOrUpdate.fulfilled]: (state, action) => {
      let data = action.payload;
      if (data && data.data && data.data.message) {
        state.snackBar = generateSnackData(true, data.data.message, "success");
      }
      state.isLoading = false;
    },
    [step3AddOrUpdate.rejected]: (state, action) => {
      state.isLoading = false;
    },
    [commonGetRequest.pending]: (state, action) => {
      state.isLoading = true;
    },
    [commonGetRequest.fulfilled]: (state, action) => {
      let data = action.payload;

      if (data && data.data && data.data.result) {
        if (data.key_name) {
          let temp_data = [];
          data.data.result.map((element) => {
            temp_data.push({ id: element.id, name: element[data.key_name] });
          });
          state[data && data.key ? data.key : "flush"] = temp_data;
        } else {
          state[data && data.key ? data.key : "flush"] = data.data.result;
        }
      } else if (data && data.data && data.data.message === "No data found") {
        state[data && data.key ? data.key : "flush"] = [];
      } else {
        state[data && data.key ? data.key : "flush"] = [];
      }
      state.isLoading = false;
    },
    [commonGetRequest.rejected]: (state, action) => {
      state.isLoading = false;
    },
    [commonPostRequest.pending]: (state, action) => {
      state.isLoading = true;
    },
    [commonPostRequest.fulfilled]: (state, action) => {
      let data = action.payload;
      console.log("data :>> ", data);
      if (data && data.data && data.data.result) {
        state[data.key] = data.data.result;
      }
      state.isLoading = false;
    },
    [commonPostRequest.rejected]: (state, action) => {
      state.isLoading = false;
    },
    [bookingAddRequest.pending]: (state, action) => {
      state.isLoading = true;
    },
    [bookingAddRequest.fulfilled]: (state, action) => {
      state.isLoading = false;
    },
    [bookingAddRequest.rejected]: (state, action) => {
      state.isLoading = false;
    },
  },
});

export const { setSnackBar, setOnboardVendorForm, setSteps, setIsLoading, setBookingConfirm } = authSlice.actions;

export default authSlice.reducer;
