import React from "react";
import { useHistory } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";

// Components
import Select from "../common/Select";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(3),
    textAlign: "left",
    color: theme.palette.text.secondary,
  },
  button: {
    borderRadius: 50,
    backgroundColor: theme.palette.base1.main,
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base1.dark,
      color: "#fff",
    },
  },
}));

export default function Header(props) {
  const { sports_list, complex_list, handleSelect } = props;
  const [formData, setFormData] = React.useState({
    sports: null,
    turf: null,
  });
  const [checkData, setCheckData] = React.useState(0);
  const classes = useStyles();
  const history = useHistory();

  const handleShow = () => {
    if (formData.sports || formData.turf) {
      setCheckData(0);
      handleSelect(formData)
    } else {
      setCheckData(1);
    }
  };

  // const SetFormData = (newData) => {
  //   console.log(newData);
  //   let newFormData = formData;
  //   if (newData.sports === "null") {
  //     newFormData.sports = null;
  //   } else {
  //     newData.sports
  //       ? (newFormData.sports = newData.sports)
  //       : (newFormData.date = newData.date);
  //   }
  //   setFormData(newFormData);
  // };

  return (
    <div className={classes.root}>
      <Paper variant="outlined">
        <Grid container>
          <Grid item lg={3} md={4} sm={12} xs={12}>
            <Paper elevation={0} className={classes.paper}>
              <Typography component="div" noWrap>
                Sports
              </Typography>
              <Select
                data={sports_list}
                changeFormData={(newData) => setFormData({sports: newData.key, turf: formData.turf})}
              />
            </Paper>
          </Grid>
          <Grid item lg={2} md={3} sm={12} xs={12}>
            <Paper elevation={0} className={classes.paper}>
              <Typography component="div" noWrap>
                Turf
              </Typography>
              <Select
                data={complex_list}
                changeFormData={(newData) => setFormData({turf: newData.key, sports: formData.sports})}
              />
            </Paper>
          </Grid>
          <Grid item lg={2} md={3} sm={12} xs={12}>
            <Paper elevation={0} className={classes.paper}>
              <Typography component="div" noWrap>
                .
              </Typography>
              <Button
                fullWidth
                size="large"
                variant="outlined"
                className={classes.button}
                onClick={handleShow}
              >
                SHOW PRICING
              </Button>
              {checkData ? (
                <Typography
                  align="center"
                  color="secondary"
                  component="div"
                  noWrap
                >
                  Please select both
                </Typography>
              ) : null}
            </Paper>
          </Grid>
        </Grid>
      </Paper>
    </div>
  );
}
