import React from "react";

import { bookingDetails, bookingList, booking_status, currencySymbol } from "../../../assets/helper/constants";

import Server from "../../../Server";
import { Grid, makeStyles, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from "@material-ui/core";
import { Router, useHistory } from "react-router";

const BookingDetails = () => {

const history = useHistory();

const useStyles = makeStyles((theme) => ({
    paper: {
      padding: theme.spacing(3),
      backgroundColor: "#F5F5F5",
      borderColor: "#fff",
      display: "flex",
    },
    paper2: {
      padding: theme.spacing(2),
      marginLeft: theme.spacing(3),
      marginTop: theme.spacing(2),
      marginBottom: theme.spacing(2),
      backgroundColor: "#F3F3F3",
    },
    typo1: {
      color: "#484848",
      verticalAlign: "middle",
      fontWeight: "bold",
    },
    typo2: {
      paddingBottom: theme.spacing(1),
      color: "#484848",
      verticalAlign: "middle",
      fontWeight: "bold",
    },
    typo3: {
      paddingBottom: theme.spacing(1),
      color: "#808080",
      verticalAlign: "middle",
    },
    leftMargin: {
      marginLeft: theme.spacing(3),
      marginTop: theme.spacing(2),
    },
    divider: {
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1),
    },
    grid1: {
      display: "flex",
    },
    right: {
      marginLeft: "auto",
    },
    icon: {
      color: "#272727",
      marginLeft: "auto",
    },
    textfield: {
      backgroundColor: "#fff",
      width: "95%",
      marginBottom: theme.spacing(1),
    },
    paddingLeft: {
      paddingLeft: theme.spacing(3),
    },
    container: {
        paddingLeft: theme.spacing(3),
        paddingRight: theme.spacing(3),
    },
    button: {
      borderRadius: 50,
      marginTop: theme.spacing(2),
      backgroundColor: theme.palette.base1.main,
      color: "#fff",
      "&:hover": {
        backgroundColor: theme.palette.base1.dark,
      },
    },
  }));
  const classes = useStyles();
  const [details, setDetails] = React.useState();

  React.useEffect(async () => {
      console.log(history);
      if (history.location.state) {
          console.log("id",history.location.state.id);
        let res = await Server.get(bookingDetails + history.location.state.id);
        if (res.data && res.data.status) {
            setDetails(res.data.result);
        } else {
            setDetails({});
        }
      }
  }, [history.location.state]);

  return (
    details ?
    <div className={classes.container}>
        <h3>Booking Details</h3>
        <Grid container spacing={2}>
            <Grid item xs={2}>
                <Typography>Booking Number:</Typography>
            </Grid>
            <Grid item xs={4}>
                <Typography><b>#{details?.bookingNo}</b></Typography>
            </Grid>
            <Grid item xs={2}>
                <Typography>Date:</Typography>
            </Grid>
            <Grid item xs={4}>
                <Typography>{details?.booking_detail?.playground_timeslot?.date}</Typography>
            </Grid>
            <Grid item xs={2}>
                <Typography>Discont Amount:</Typography>
            </Grid>
            <Grid item xs={4}>
                <Typography>{currencySymbol + details?.discount_amount}</Typography>
            </Grid>
            <Grid item xs={2}>
                <Typography>Timing:</Typography>
            </Grid>
            <Grid item xs={4}>
                <Typography>{details?.booking_detail?.playground_timeslot?.from_time} to {details?.booking_detail?.playground_timeslot?.to_time}</Typography>
            </Grid>
            <Grid item xs={2}>
                <Typography>Total Amount:</Typography>
            </Grid>
            <Grid item xs={4}>
                <Typography>{currencySymbol + details?.total_amount}</Typography>
            </Grid>
            <Grid item xs={2}>
                <Typography>Status:</Typography>
            </Grid>
            <Grid item xs={4}>
                <Typography style={booking_status[details?.state_id] === "Pending" ? {color: "orange"} : (booking_status[details?.state_id] === "Cancelled" ? {color: "red"} : (booking_status[details?.state_id] === "Confirmed" ? {color: "green"} : {}))}>{booking_status[details?.state_id]}</Typography>
            </Grid>
            <Grid item xs={2}>
                <Typography>Sport:</Typography>
            </Grid>
            <Grid item xs={4}>
                <Typography>{details?.complex_playground?.sport?.name ? details?.complex_playground?.sport?.name : "-"}</Typography>
            </Grid>
            <Grid item xs={2}>
                <Typography>Ground:</Typography>
            </Grid>
            <Grid item xs={4}>
                <Typography>{details?.complex_playground?.playground_name}</Typography>
            </Grid>
        </Grid>
        
        <h3>Player Details</h3>
        <TableContainer component={Paper}>
            <Table sx={{ minWidth: 650 }} aria-label="simple table">
                <TableHead>
                    <TableRow>
                        <TableCell align="left">Name</TableCell>
                        <TableCell align="left">Email</TableCell>
                        <TableCell align="left">Contact Number</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                {details?.booking_detail?.player_details.map((row) => (
                    <TableRow
                    key={row?.id}
                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                    >
                        <TableCell align="left" component="th" scope="row?">
                            {row?.user?.name ? row?.user?.name : "-"}
                        </TableCell>
                        <TableCell align="left" component="th" scope="row?">
                            {row?.user?.login?.email ? row?.user?.login?.email : "-"}
                        </TableCell>
                        <TableCell align="left" component="th" scope="row?">{row?.user?.login?.mobile ? row?.user?.login?.mobile : "-"}</TableCell>
                    </TableRow>
                ))}
                </TableBody>
            </Table>
            </TableContainer>
    </div>
    : null
);
};

export default BookingDetails;
