import React from "react";
import Table from "../../../common/Table2";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";

import { useHistory } from "react-router";

import { useDispatch, useSelector } from "react-redux";
import { commonGetRequest, setSnackBar, setIsLoading } from "../../../../redux/features/Auth";

import Server from "../../../../Server";

import {
    memberUrl,
    generateSnackData,
    memberAllUrl,
} from "../../../../assets/helper/constants";

const useStyles = makeStyles((theme) => ({
    typo1: {
        color: "#F3555E",
        fontWeight: "600",
    },
    typo2: {
        color: "#808080",
        fontWeight: "500",
        fontSize: "18px",
    },
    typo3: {
        color: "#808080",
        fontWeight: "700",
        fontSize: "18px",
    },
}));

const columns = [
    { id: "id", label: "Member ID", minWidth: 100 },
    { id: "name", label: "Member Name", minWidth: 120 },
    { id: "phone", label: "Phone Number", minWidth: 100 },
    {
        id: "email",
        label: "Email ID",
        minWidth: 120,
        align: "center",
    },
    {
        id: "memtype",
        label: "Membership Type",
        minWidth: 120,
    },
    {
        id: "duration",
        label: "Duration",
        minWidth: 120,
    },
    {
        id: "action",
        label: "Action",
        minWidth: 120,
    },
];

const List = (props) => {
    const classes = useStyles();

    const { membersData, isLoading } = useSelector(
        (state) => state.auth
    );
    const dispatch = useDispatch();

    React.useEffect(() => {
        dispatch(
            commonGetRequest({
                url: memberUrl,
                key: "membersData",
                query: props.searchVal ? "?search=" + props.searchVal : ""
            })
        );
    }, [dispatch, props.searchVal]);

    const styleId = (id) => {
        return (
            <Typography variant="h5" className={classes.typo1}>
                {id}
            </Typography>
        );
    };

    const styleOthers = (value, bold) => {
        return <Typography className={bold ? classes.typo3 : classes.typo2}>{value}</Typography>;
    };

    const deleteRow = async (id) => {
        if (window.confirm("Are you sure you want to delete this?")) {
            dispatch(setIsLoading());
            try {
                let res = await Server.delete(memberAllUrl + "?id=" + id);

                if (res.data && res.data.status) {
                    dispatch(setSnackBar(generateSnackData(true, res.data.message, "success")));
                } else {
                    dispatch(setSnackBar(generateSnackData(true, res.data.message, "error")));
                }
                dispatch(setIsLoading());
                dispatch(
                    commonGetRequest({
                        url: memberUrl,
                        key: "membersData",
                        query: props.searchVal ? "?search=" + props.searchVal : ""
                    })
                );
            } catch (error) {
                const resp =
                    error.response && error.response.data.message
                        ? error.response.data.message
                        : "Couldn't connect to our servers.";

                dispatch(setSnackBar(generateSnackData(true, resp, "error")));
                dispatch(setIsLoading());
            }
        }
    }

    const history = useHistory();
    const editRow = (id) => {
        history.push("/members/add", { id: id })
    }

    const styleAction = (id) => {
        return (
            <div>
                <IconButton onClick={() => deleteRow(id)} className={classes.icon} aria-label="create pricing" component="span">
                    <DeleteOutlineIcon fontSize="medium" />
                </IconButton>
                <IconButton onClick={() => editRow(id)} className={classes.icon} aria-label="create pricing" component="span">
                    <EditOutlinedIcon fontSize="medium" />
                </IconButton>
            </div>
        );
    };

    function createData(oldid, oldname, oldphone, oldemail, oldmemtype, oldduration, oldaction) {
        const id = styleId(oldid);
        const name = styleOthers(oldname);
        const phone = styleOthers(oldphone);
        const email = styleOthers(oldemail);
        const duration = styleOthers(oldduration);
        const memtype = styleOthers(oldmemtype, 1);
        const action = styleAction(oldaction);
        return { id, name, phone, email, memtype, duration, action };
    }
    let rows = [];

    if (!isLoading && membersData) {
        membersData.map(function (memData) {
            rows.push(
                createData(
                    memData.id,
                    memData.user.name,
                    memData.user.login.mobile,
                    memData.user.login.email,
                    memData.user.membership_payment.membership_package.package_name,
                    memData.user.membership_payment.membership_package.duration,
                    memData.id
                )
            )
            return true;
        })
    }

    // const rows = [
    //     createData(
    //         6273563,
    //         "Raj Singhal",
    //         "91233331",
    //         "raj@gmail.com",
    //         "5 years package",
    //         "Edit Delete"
    //     ),
    //     createData(
    //         6273563,
    //         "Raj Singhal",
    //         "91233331",
    //         "raj@gmail.com",
    //         "5 years package",
    //         "Edit Delete"
    //     ),
    // ];
    return <Table columns={columns} rows={rows} />;
};

export default List;
