import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import { Divider } from "@material-ui/core";
import Radio from "@material-ui/core/Radio";
import Button from "@material-ui/core/Button";

import DatePicker from "../../common/DatePicker";
import Select from "../../common/Select";
import SelectPricing from "../../common/SelectPricing";
import WeekCheckBox from "../../common/WeekCheckBox";


import { useHistory } from "react-router-dom";
import { Form, Field } from "react-final-form";
import OutlinedSelect from "../../common/Input/OutlinedSelect";
import Input from "../../common/Input/LoginOutlinedInput";

import Server from "../../../Server";
import { useDispatch, useSelector } from "react-redux";
import { setSnackBar, setIsLoading } from "../../../redux/features/Auth";
import { commonGetRequest } from "../../../redux/features/Auth";

import {
    offerAdd,
    offerUpdate,
    offerGet,
    sportsListUrl,
    generateSnackData
} from "../../../assets/helper/constants";


function convertDate(inputFormat) {
    function pad(s) { return (s < 10) ? '0' + s : s; }
    var d = new Date(inputFormat)
    return [d.getFullYear(), pad(d.getMonth() + 1), pad(d.getDate())].join('-')
}

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(3),
    },
    grid: {
        paddingTop: theme.spacing(5),
    },
    typo3: {
        color: "#484848",
        textAlign: "left",
        fontSize: "17px",
        fontWeight: "600",
        marginBottom: theme.spacing(0.5),
        marginTop: theme.spacing(1),
    },
    typo4: {
        color: "#808080",
        fontSize: "14px",
    },
    paddingRight: {
        paddingRight: theme.spacing(2),
    },
    textField: {
        width: "100%",
    },
    divider: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
    },
    button: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
        minWidth: '140px',
        backgroundColor: '#272727',
        color: '#fff'
    }
}));

// const weekData = [
//     {
//         id: "09-00 AM to 10",
//         name: "09-00 AM",
//     },
// ];

const Forms = () => {
    const dispatch = useDispatch();
    const history = useHistory();

    const { isLoading, sportsData, sportsUniqueData } = useSelector(
        (state) => state.auth
    );

    let [globalForm, setGlobalForm] = React.useState(null);
    const [sportsId, setSportsId] = React.useState(null);
    const [fromDate, setFromDate] = React.useState(convertDate(new Date()));
    const [toDate, setToDate] = React.useState(convertDate(new Date()));
    const [selectedValue, setSelectedValue] = React.useState("cash");
    const [multipleTime, setMultipleTime] = React.useState("no");
    const [days, setDays] = React.useState([]);

    React.useEffect(() => {
        dispatch(
            commonGetRequest({
                url: sportsListUrl,
                key: "sportsData",
                query: ""
            })
        );
    }, [dispatch]);

    React.useEffect(() => {
        if (history.location.state) {
            dispatch(
                commonGetRequest({
                    url: offerGet,
                    key: "sportsUniqueData",
                    query: "/" + history.location.state.id
                })
            );
        }
    }, [dispatch, history.location.state])

    React.useEffect(() => {
        if (sportsUniqueData) {
            setDays(JSON.parse(sportsUniqueData.days));
            setFromDate(convertDate(sportsUniqueData.from_date));
            setToDate(convertDate(sportsUniqueData.to_date));
            setSelectedValue(sportsUniqueData.amount ? "cash" : "percent");
            setMultipleTime(sportsUniqueData.is_multiple_times ? "yes" : "no");
            setGlobalForm({
                id: sportsUniqueData.id,
                name: sportsUniqueData.offerName,
                sport_id: sportsUniqueData.sport_id,
                from_time: sportsUniqueData.from_time,
                to_time: sportsUniqueData.to_time,
                amount: sportsUniqueData.amount,
                remarks: sportsUniqueData.remarks,
                coupon_count: sportsUniqueData.coupon_count,

            });
        }
    }, [sportsUniqueData])

    const classes = useStyles();

    const required = (value) => (value ? undefined : "* Required");

    const name_ = ({ input, meta }) => {
        return (
            <Input
                id="name"
                label="Enter coupon name"
                input={input}
                meta={meta}
            />
        );
    };

    const from_time_ = ({ input, meta }) => {
        return (
            <Input
                id="from_time"
                input={input}
                meta={meta}
                type="time"
            />
        );
    };

    const to_time_ = ({ input, meta }) => {
        return (
            <Input
                id="to_time"
                input={input}
                meta={meta}
                type="time"
            />
        );
    };

    const amount_ = ({ input, meta }) => {
        return (
            <Input
                id="amount"
                input={input}
                meta={meta}
            />
        );
    };

    const remarks_ = ({ input, meta }) => {
        return (
            <Input
                id="remarks"
                input={input}
                meta={meta}
                label="Coupon Remarks here"
            />
        );
    };

    const coupon_count_ = ({ input, meta }) => {
        return (
            <Input
                id="coupon_count"
                input={input}
                meta={meta}
                label="Coupon count"
            />
        );
    };

    const sport_id_ = ({ input, meta }) => {
        let spData = [];
        sportsData && sportsData.map(function (sData) {
            spData.push({ id: sData.id, name: sData.name })
            return false;
        })
        return (
            <OutlinedSelect
                id="sport_id"
                key_name="sport_id"
                label="Select Sport"
                input={input}
                meta={meta}
                handleNext={(value, id) => console.log(value, "value")}
                returnValue={(value) => setSportsId(value)}
                data={spData}
            />
        );
    };

    const daysChange = (val) => {
        let newDay = days;
        if (days.includes(val)) {
            var index = newDay.indexOf(val);
            if (index !== -1) {
                newDay.splice(index, 1);
            }
        } else {
            newDay.push(val);
        }
        setDays(newDay);
    }

    const onSubmit = async (values) => {
        values.from_date = fromDate;
        values.to_date = toDate;
        if (selectedValue === 'percent') {
            values.discount_percent = values.amount
        }
        values.is_multiple_times = multipleTime === 'no' ? 0 : 1;
        values.days = days;
        dispatch(setIsLoading());
        setGlobalForm(values);
        try {
            let res = [];
            if (globalForm && globalForm.id) {
                res = await Server.post(offerUpdate, values)
            } else {
                res = await Server.post(offerAdd, values)
            }

            if (res.data && res.data.status) {
                dispatch(setSnackBar(generateSnackData(true, res.data.message, "success")));
                history.push("/offers");
            } else {
                dispatch(setSnackBar(generateSnackData(true, res.data.message, "error")));
            }
            dispatch(setIsLoading());
        } catch (error) {
            const resp =
                error.response && error.response.data.message
                    ? error.response.data.message
                    : "Couldn't connect to our servers.";

            dispatch(setSnackBar(generateSnackData(true, resp, "error")));
            dispatch(setIsLoading());
        }
    }

    return (
        <Paper className={classes.paper}>
            {
                !isLoading
                    ?
                    <Form
                        initialValues={globalForm}
                        onSubmit={onSubmit}
                        render={({ handleSubmit, submitting, values }) => (
                            <form onSubmit={handleSubmit}>
                                <Grid className={classes.grid}>
                                    <Grid container lg={12}>
                                        <Grid lg={4} className={classes.paddingRight}>
                                            <Typography className={classes.typo3}>COUPON NAME</Typography>
                                            <Field
                                                className={classes.textField}
                                                variant="outlined"
                                                size="small"
                                                name="name"
                                                component={name_}
                                                validate={required}
                                            />
                                        </Grid>
                                        <Grid lg={4}>
                                            <Typography className={classes.typo3} component="div" noWrap>
                                                Sports
                                            </Typography>
                                            <Field
                                                className={classes.textField}
                                                variant="outlined"
                                                size="small"
                                                name="sport_id"
                                                component={sport_id_}
                                                validate={required}
                                            />
                                        </Grid>
                                    </Grid>
                                    <Grid container lg={12}>
                                        <Grid lg={4} className={classes.paddingRight}>
                                            <Typography className={classes.typo3} component="div" noWrap>
                                                From
                                            </Typography>
                                            <DatePicker fullWidth={1} name="from_date" value={fromDate} changeFormData={(newData) => setFromDate(convertDate(newData.date))} />
                                        </Grid>
                                        <Grid lg={4}>
                                            <Typography className={classes.typo3} component="div" noWrap>
                                                To
                                            </Typography>
                                            <DatePicker fullWidth={1} name="to_date" value={toDate} changeFormData={(newData) => setToDate(convertDate(newData.date))} />
                                        </Grid>
                                    </Grid>
                                    <Divider className={classes.divider} />
                                    <Grid container lg={12}>
                                        <Grid lg={4} className={classes.paddingRight}>
                                            <Grid lg={8}>
                                                <Typography className={classes.typo3} component="div" noWrap>
                                                    From Time
                                                </Typography>
                                                <Field
                                                    className={classes.textField}
                                                    name="from_time"
                                                    component={from_time_}
                                                    validate=""
                                                />
                                                <Typography className={classes.typo3} component="div" noWrap>
                                                    To Time
                                                </Typography>
                                                <Field
                                                    className={classes.textField}
                                                    name="to_time"
                                                    component={to_time_}
                                                    validate=""
                                                />
                                            </Grid>
                                        </Grid>
                                        <Grid lg={4} className={classes.paddingRight}>
                                            <Typography className={classes.typo3} component="div" noWrap>
                                                DAY
                                            </Typography>
                                            <WeekCheckBox name="days" value={days} validate="" returnValue={(e) => daysChange(e)} />
                                        </Grid>
                                    </Grid>
                                    <Divider className={classes.divider} />
                                    <Grid container lg={12}>
                                        <Grid lg={4}>
                                            <Typography className={classes.typo3} component="div" noWrap>
                                                TYPE
                                            </Typography>
                                            <Radio
                                                checked={selectedValue === "cash"}
                                                onChange={() => setSelectedValue("cash")}
                                                value="cash"
                                                name="radio-button-demo"
                                                inputProps={{ "aria-label": "A" }}
                                                label="Cash Discount"
                                            />
                                            Cash Discount
                                            <Radio
                                                checked={selectedValue === "percent"}
                                                onChange={() => setSelectedValue("percent")}
                                                value="percent"
                                                name="radio-button-demo"
                                                inputProps={{ "aria-label": "B" }}
                                                label="Percentage Discount"
                                            />
                                            Percentage Discount
                                            <Field
                                                className={classes.textField}
                                                name="amount"
                                                component={amount_}
                                                validate={required}
                                            />
                                        </Grid>
                                        <Grid lg={4}>
                                            <Typography className={classes.typo3} component="div" noWrap>
                                                PERMANENT
                                            </Typography>
                                            <Typography className={classes.typo4}>
                                                Yes (If yes, coupon can be used multiple times over specified Period)
                                            </Typography>
                                            <Radio
                                                checked={multipleTime === "no"}
                                                onChange={() => setMultipleTime("no")}
                                                value="no"
                                                name="radio-button-demo"
                                                inputProps={{ "aria-label": "A" }}
                                            />
                                            No
                                            <Radio
                                                checked={multipleTime === "yes"}
                                                onChange={() => setMultipleTime("yes")}
                                                value="yes"
                                                name="radio-button-demo"
                                                inputProps={{ "aria-label": "B" }}
                                            />
                                            Yes
                                        </Grid>
                                    </Grid>
                                    <Divider className={classes.divider} />
                                    <Grid container lg={6}>
                                        <Typography className={classes.typo3} component="div" noWrap>
                                            REMARKS
                                        </Typography>
                                        <Field
                                            className={classes.textField}
                                            name="remarks"
                                            component={remarks_}
                                            validate={required}
                                        />
                                    </Grid>
                                    <Grid container lg={4}>
                                        <Typography className={classes.typo3} component="div" noWrap>
                                            COUPON COUNT
                                        </Typography>
                                        <Field
                                            className={classes.textField}
                                            name="coupon_count"
                                            component={coupon_count_}
                                            validate=""
                                        />
                                    </Grid>
                                    <Button variant="contained" type="submit" disabled={submitting ? submitting : false} className={classes.button}>Confirm</Button>
                                </Grid>
                            </form>
                        )
                        }
                    />
                    :
                    null
            }
        </Paper>
    );
};

export default Forms;
