import React, { Fragment, useState } from "react";
import { Divider, Paper } from "@material-ui/core";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import FormGroup from "@material-ui/core/FormGroup";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Radio from "@material-ui/core/Radio";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import Button from "@material-ui/core/Button";

import { useHistory } from "react-router-dom";
import { Form, Field } from "react-final-form";
import OutlinedSelect from "../../../common/Input/OutlinedSelect";
import Input from "../../../common/Input/LoginOutlinedInput";

import Server from "../../../../Server";
import { useDispatch, useSelector } from "react-redux";
import { setSnackBar, setIsLoading } from "../../../../redux/features/Auth";
import { commonGetRequest } from "../../../../redux/features/Auth";

import {
    memberAllUrl,
    memberShipPackageUrl,
    generateSnackData
} from "../../../../assets/helper/constants"

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(3),
    },
    typo1: {
        color: "#484848",
        fontSize: "16px",
        fontWeight: "600",
    },
    typo2: {
        color: "#464646",
        fontSize: "20px",
    },
    textField: {
        width: "95%",
        marginTop: theme.spacing(1),
    },
    grid: {
        marginTop: theme.spacing(2),
    },
    grid2: {
        marginTop: theme.spacing(0.5),
        display: "flex",
        marginRight: theme.spacing(3),
    },
    grid3: {
        marginLeft: "auto",
    },
    divider: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(2),
    },
    button: {
        borderRadius: 50,
        marginTop: theme.spacing(2),
        backgroundColor: theme.palette.base1.main,
        color: "#fff",
        "&:hover": {
            backgroundColor: theme.palette.base1.dark,
        }
    }
}));

const Forms = () => {
    const dispatch = useDispatch();
    const history = useHistory();

    const [gender, setGender] = useState("male");
    const [membershipPackage, setMembershipPackage] = useState(null);

    const { isLoading, membership_package, members_data } = useSelector(
        (state) => state.auth
    );

    let [globalForm, setGlobalForm] = React.useState(null);

    React.useEffect(() => {
        dispatch(
            commonGetRequest({
                url: memberShipPackageUrl,
                key: "membership_package",
                query: ""
            })
        );
    }, [dispatch]);

    React.useEffect(() => {
        if (history.location.state) {
            dispatch(
                commonGetRequest({
                    url: memberAllUrl,
                    key: "members_data",
                    query: "?id=" + history.location.state.id
                })
            );
        }
    }, [dispatch, history.location.state])

    React.useEffect(() => {
        if (members_data) {
            setGlobalForm({
                id: members_data.id,
                name: members_data.user.name,
                mobile: members_data.user.login.mobile,
                email: members_data.user.login.email,
                gender: members_data.user.gender,
                membership_package_id: members_data.user.membership_payment.membership_package.id,
                payment_mode: members_data.user.membership_payment.payment_mode,
                paid_amount: members_data.user.membership_payment.paid_amount ? members_data.user.membership_payment.paid_amount + "" : '0',
                handler_name: members_data.user.membership_payment.handler_name,
            });
            setGender(members_data.user.gender);
        }
    }, [members_data])

    const required = (value) => (value ? undefined : "* Required");

    const classes = useStyles();

    const name_ = ({ input, meta }) => {
        return (
            <Input
                id="name"
                label="Enter member name"
                input={input}
                meta={meta}
            />
        );
    };

    const mobile_ = ({ input, meta }) => {
        return (
            <Input
                id="mobile"
                label="Enter member mobile"
                input={input}
                meta={meta}
            />
        );
    };

    const membership_package_id_ = ({ input, meta }) => {
        let memData = [];
        membership_package && membership_package.map(function (memShipData) {
            memData.push({ id: memShipData.id, name: memShipData.package_name })
            return false;
        })
        return (
            <OutlinedSelect
                id="membership_package_id"
                key_name="membership_package_id"
                label="Select Membership Package"
                input={input}
                meta={meta}
                handleNext={(value, id) => console.log(value, "value")}
                returnValue={(value) => setMembershipPackage(value)}
                data={memData}
            />
        );
    };

    const payment_mode_ = ({ input, meta }) => {
        let memData = [];
        memData.push({ id: "CASH", name: "Cash" })
        memData.push({ id: "CARD", name: "Card" })
        return (
            <OutlinedSelect
                id="payment_mode"
                key_name="payment_mode"
                label="Select Payment Type"
                input={input}
                meta={meta}
                handleNext={(value, id) => console.log(value, "value")}
                returnValue={(value) => console.log(value)}
                data={memData}
            />
        );
    };

    const email_ = ({ input, meta }) => {
        return (
            <Input
                id="email"
                label="Enter member email"
                input={input}
                meta={meta}
            />
        );
    };

    const paid_amount_ = ({ input, meta }) => {
        return (
            <Input
                id="paid_amount"
                label="Enter Paid Amount"
                input={input}
                meta={meta}
            />
        );
    };
    const handler_name_ = ({ input, meta }) => {
        return (
            <Input
                id="handler_name"
                label="Enter Handler Name"
                input={input}
                meta={meta}
            />
        );
    };

    const onSubmit = async (values) => {
        dispatch(setIsLoading());
        values.gender = gender;
        setGlobalForm(values);
        try {
            let res = [];
            if (globalForm && globalForm.id) {
                res = await Server.put(memberAllUrl, values)
            } else {
                res = await Server.post(memberAllUrl, values)
            }

            if (res.data && res.data.status) {
                dispatch(setSnackBar(generateSnackData(true, res.data.message, "success")));
                history.push("/members");
            } else {
                dispatch(setSnackBar(generateSnackData(true, res.data.message, "error")));
            }
            dispatch(setIsLoading());
        } catch (error) {
            const resp =
                error.response && error.response.data.message
                    ? error.response.data.message
                    : "Couldn't connect to our servers.";

            dispatch(setSnackBar(generateSnackData(true, resp, "error")));
            dispatch(setIsLoading());
        }
    }

    return (
        <Paper className={classes.paper}>
            {
                !isLoading
                    ?
                    <Form
                        initialValues={globalForm}
                        onSubmit={onSubmit}
                        render={({ handleSubmit, submitting, values }) => (
                            <form onSubmit={handleSubmit}>
                                <Grid className={classes.grid} container lg={8}>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>MEMBER NAME</Typography>
                                        <Field
                                            className={classes.textField}
                                            variant="outlined"
                                            size="small"
                                            name="name"
                                            component={name_}
                                            validate={required}
                                        />
                                    </Grid>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>PHONE NUMBER</Typography>
                                        <Field
                                            className={classes.textField}
                                            variant="outlined"
                                            size="small"
                                            name="mobile"
                                            component={mobile_}
                                            validate={required}
                                        />
                                    </Grid>
                                </Grid>
                                <Grid className={classes.grid} container lg={8}>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>EMAIL ID</Typography>
                                        <Field
                                            className={classes.textField}
                                            variant="outlined"
                                            size="small"
                                            name="email"
                                            component={email_}
                                            validate={required}
                                        />
                                    </Grid>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>MEMBERSHIP TYPE</Typography>
                                        <Field
                                            className={classes.textField}
                                            variant="outlined"
                                            size="small"
                                            name="membership_package_id"
                                            component={membership_package_id_}
                                            validate={required}
                                        />
                                    </Grid>
                                </Grid>
                                <Grid className={classes.grid} container lg={8}>
                                    {/* <Grid lg={6}>
                                        <Typography className={classes.typo1}>ADDRESS</Typography>
                                        <TextField
                                            className={classes.textField}
                                            id="address"
                                            label="F-402..."
                                            defaultValue=""
                                            variant="outlined"
                                            size="small"
                                            multiline
                                            rows={4}
                                            type="input"
                                        />
                                    </Grid> */}
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>GENDER</Typography>
                                        <FormControl
                                            className={classes.textField}
                                            required
                                            component="fieldset"
                                        >
                                            <RadioGroup
                                                aria-label="gender"
                                                defaultValue={(gender) ? gender : 'female'}
                                                name="radio-buttons-group"
                                            >
                                                <FormControlLabel
                                                    control={<Radio onClick={(e) => setGender(e.target.value)} name="gender" value="male" />}
                                                    label="Male"
                                                />
                                                <FormControlLabel
                                                    control={<Radio onClick={(e) => setGender(e.target.value)} name="gender" value="female" />}
                                                    label="Female"
                                                />
                                                <FormControlLabel
                                                    control={
                                                        <Radio onClick={(e) => setGender(e.target.value)} name="gender" value="other" />
                                                    }
                                                    label="Other"
                                                />
                                            </RadioGroup>
                                        </FormControl>
                                    </Grid>
                                    {
                                        membershipPackage
                                            ?
                                            membership_package.map(function (memPack, memKey) {
                                                if (memPack.id !== membershipPackage) {
                                                    return false;
                                                }
                                                return (
                                                    <Fragment>
                                                        <Grid className={classes.grid2} container lg={12}>
                                                            <Typography variant="h6" className={classes.typo1}>
                                                                PAYMENT DETAILS
                                                            </Typography>
                                                        </Grid>
                                                        <Grid className={classes.grid2} container lg={12}>
                                                            <Grid>
                                                                <Typography className={classes.typo1}>{memPack.package_name}</Typography>
                                                            </Grid>
                                                            <Grid className={classes.grid3}>
                                                                <Typography className={classes.typo2}>{memPack.amount}</Typography>
                                                            </Grid>
                                                        </Grid>
                                                        <Grid className={classes.grid2} container lg={12}>
                                                            <Grid>
                                                                <Typography className={classes.typo1}>GST(18%)</Typography>
                                                            </Grid>
                                                            <Grid className={classes.grid3}>
                                                                <Typography className={classes.typo2}>{(parseInt(memPack.amount) * 18) / 100}</Typography>
                                                            </Grid>
                                                        </Grid>
                                                        <Divider className={classes.divider} />
                                                        <Grid className={classes.grid2} container lg={12}>
                                                            <Grid>
                                                                <Typography className={classes.typo1}>Total amount</Typography>
                                                            </Grid>
                                                            <Grid className={classes.grid3}>
                                                                <Typography className={classes.typo2}>{((parseInt(memPack.amount) * 18) / 100) + parseInt(memPack.amount)}</Typography>
                                                            </Grid>
                                                        </Grid>
                                                    </Fragment>
                                                )
                                            })
                                            :
                                            null
                                    }
                                </Grid>
                                <Grid className={classes.grid} container lg={8}>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>PAYMENT TYPE</Typography>
                                        <Field
                                            className={classes.textField}
                                            variant="outlined"
                                            size="small"
                                            name="payment_mode"
                                            component={payment_mode_}
                                            validate={required}
                                        />
                                    </Grid>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>.</Typography>
                                        <Field
                                            className={classes.textField}
                                            variant="outlined"
                                            size="small"
                                            name="paid_amount"
                                            component={paid_amount_}
                                            validate={required}
                                        />
                                    </Grid>
                                </Grid>
                                <Grid className={classes.grid} container lg={8}>
                                    <Grid lg={6}>
                                        <Typography className={classes.typo1}>HANDLER'S NAME</Typography>
                                        <Field
                                            className={classes.textField}
                                            variant="outlined"
                                            size="small"
                                            name="handler_name"
                                            component={handler_name_}
                                            validate={required}
                                        />
                                    </Grid>
                                </Grid>
                                <Button type="submit" disabled={submitting ? submitting : false} className={classes.button} variant="contained">
                                    CONFIRM MEMBERSHIP
                                </Button>
                            </form>
                        )
                        }
                    />
                    :
                    null
            }
        </Paper >
    );
};

export default Forms;
