import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import AddCircleIcon from "@material-ui/icons/AddCircle";
import IconButton from "@material-ui/core/IconButton";
import { useHistory } from "react-router"

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(3),
        textAlign: "left",
        color: theme.palette.text.secondary,
        backgroundColor: "#FCFCFC",
    },
    typo1: {
        color: "#484848",
        fontSize: "17px",
        fontWeight: "600",
    },
    typo2: {
        paddingLeft: theme.spacing(1),
        color: "#41BEAE",
        fontSize: "15px",
        fontWeight: "600",
    },
    button: {
        top: 12,
        paddingLeft: theme.spacing(3),
        paddingRight: theme.spacing(3),
        marginLeft: theme.spacing(2),
        borderRadius: 50,
        backgroundColor: "#41BEAE",
        color: "#fff",
        "&:hover": {
            backgroundColor: "#34988b",
            color: "#fff",
        },
    },
    TextField: {
        width: "40%",
    },
    grid1: {
        display: "flex",
    },
    grid2: {
        marginLeft: "auto",
    },
    icon: {
        color: "#41BEAE",
    },
}));

export default function Header(props) {
    const [searchVal, setSearchVal] = React.useState(null);
    const classes = useStyles();
    const history = useHistory();
    const addMember = () => {
        history.push("/offers/add")
    }
    return (
        <Paper className={classes.paper} variant="outlined">
            <Grid lg={12}>
                <Typography className={classes.typo1} variant="h6" component="h6">
                    Find Coupon
                </Typography>
            </Grid>
            <Grid className={classes.grid1} container>
                <Grid lg={6} xs={12}>
                    <TextField
                        shrink={false}
                        className={classes.TextField}
                        id="name"
                        label="Diwali Offer"
                        type="input"
                        onChange={(e) => setSearchVal(e.target.value)}
                    />
                    <Button className={classes.button} variant="contained" onClick={() => props.setSearchVal(searchVal)}>
                        Search
                    </Button>
                </Grid>
                <Grid lg={4} xs={12} className={classes.grid2}>
                    <IconButton onClick={addMember} className={classes.icon} aria-label="create pricing" component="span">
                        <AddCircleIcon fontSize="large" className={classes.icon} />
                        <Typography className={classes.typo2}>ADD COUPON</Typography>
                    </IconButton>
                </Grid>
            </Grid>
        </Paper>
    );
}