import React from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import { getVendorComplexByID } from "../../assets/helper/constants";
import { useSelector, useDispatch } from "react-redux";
import { commonGetRequest } from "../../redux/features/Auth";
import { isAuth } from "../../assets/helper/auth";

const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(0.5),
    borderRadius: 50,
    textTransform: "none",
    padding: theme.spacing(1.2),
    minWidth: "15%",
    backgroundColor: theme.palette.base2.main,
    marginRight: theme.spacing(2),
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base2.dark,
    },
  },
  button2: {
    margin: theme.spacing(0.5),
    borderRadius: 50,
    textTransform: "none",
    padding: theme.spacing(1.2),
    minWidth: "15%",
    borderColor: theme.palette.base2.main,
    marginRight: theme.spacing(2),
    color: "#272727",
    "&:hover": {
      borderColor: theme.palette.base2.dark,
    },
  },
}));

const CourtButton = (props) => {
  const { getSelectedID } = props;
  const { playgrounds_list } = useSelector((state) => state.auth);
  const dispatch = useDispatch();
  const classes = useStyles();
  const [turfData, setTurfData] = React.useState([]);
  const [id, changeId] = React.useState(null);
  const [complexId] = React.useState(isAuth().vendor_complex_id);

  const handleChange = (id) => {
    changeId(id);
    getSelectedID(id);
  };

  React.useEffect(() => {
    dispatch(
      commonGetRequest({
        url: getVendorComplexByID,
        query: complexId,
        key: "playgrounds_list",
      })
    );
  }, []);

  React.useEffect(() => {
    if (playgrounds_list.playground) {
      setTurfData(playgrounds_list.playground);
      // changeId(playgrounds_list.playground[0].id);
    }
  }, [playgrounds_list]);

  return turfData.map((turf) => {
    return (
      <Button
        className={id === turf.id ? classes.button : classes.button2}
        id={id}
        variant={id === turf.id ? "contained" : "outlined"}
        onClick={() => handleChange(turf.id)}
      >
        {turf.playground_name}
      </Button>
    );
  });
};

export default CourtButton;
