import React from "react";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme)=>({
    textAlignCenter: {
        textAlign: 'center',
    },
    button: {
        borderRadius: 50,
        borderColor: '#41BEAE',
        color: "#41BEAE",
        "&:hover": {
          borderColor: theme.palette.base1.dark,
          color: "#34988b",
        },
    }
}))

export default function AlertDialog(props) {
  const { open, setFunctionOpen, title, description } = props;
  const classes = useStyles();

  const handleClickOpen = () => {
    setFunctionOpen(true);
  };

  const handleClose = () => {
    setFunctionOpen(false);
  };

  return (
    <div>
      {/* <Button variant="outlined" color="primary" onClick={handleClickOpen}>
        Open alert dialog
      </Button> */}
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle className={classes.textAlignCenter } id="alert-dialog-title">{title}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {description}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button className={classes.button} onClick={handleClose} color="primary" variant="outlined">
            Cancel
          </Button>
          <Button className={classes.button} onClick={handleClickOpen} color="primary" variant="outlined" autoFocus>
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
