import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  commonGetRequest,
  step2AddOrUpdate,
  setSteps,
} from "../../redux/features/Auth";
import { Form, Field } from "react-final-form";
import { makeStyles } from "@material-ui/core/styles";
import { Typography } from "@material-ui/core";
import { Grid } from "@material-ui/core";

import Input from "../common/Input/LoginOutlinedInput";
import OutlinedSelect from "../common/Input/OutlinedSelect";
import Submit from "../common/Button/BlueContainedRounded";

import { subTypesUrl, getPolicyUrl } from "../../assets/helper/constants";

const useStyles = makeStyles((theme) => ({
  typo1: {
    fontSize: "19px",
    textTransform: "uppercase",
    fontWeight: 700,
  },
  typo2: {
    paddingTop: theme.spacing(1),
    fontSize: "15px",
    fontWeight: 500,
    [theme.breakpoints.up("lg")]: {
      width: "42%",
    },
  },
  typo3: {
    textTransform: "uppercase",
    fontSize: "16px",
    fontWeight: 700,
  },
  grid1: {
    paddingTop: theme.spacing(5),
  },
  grid2: {
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "30%",
      paddingRight: theme.spacing(4),
    },
  },
  grid3: {
    width: "30%",
  },
  grid4: {
    paddingTop: theme.spacing(0.8),
  },
  paddingTop: {
    paddingTop: theme.spacing(2),
  },
}));

const Step2 = () => {
  const { sub_types, policy_data, complex_data, steps } = useSelector(
    (state) => state.auth
  );
  const [select, setSelect] = React.useState("none");
  const [globalForm, setGlobalForm] = React.useState([]);
  const classes = useStyles();
  const dispatch = useDispatch();

  const loadInitialValues = () => {
    dispatch(
      commonGetRequest({
        url: subTypesUrl,
        key: "sub_types",
        query: 12,
      })
    );
    dispatch(
      commonGetRequest({
        url: getPolicyUrl,
        key: "policy_data",
        query: complex_data.complex_id,
      })
    );
  };

  React.useEffect(() => {
    loadInitialValues();
  }, []);

  const setSelectChange = (value) => {
    if (value !== "none" && policy_data) {
      const filteredSelect = policy_data.filter(
        (ele) => ele["sub_type.id"] === value
      );
      setGlobalForm({
        policy_type_id: value,
        policy_text:
          filteredSelect && filteredSelect[0]
            ? filteredSelect[0].policy_text
            : "",
      });
    }
  };

  const required = (value) => (value ? undefined : "* Required");

  const policy_type_id = ({ input, meta }) => {
    return (
      <OutlinedSelect
        id="policy_type_id"
        data={sub_types && sub_types.subtypes ? sub_types.subtypes : []}
        label="Select Policy Type"
        key_name="name"
        input={input}
        meta={meta}
        returnValue={(value) => {
          setSelect(value);
          setSelectChange(value);
        }}
      />
    );
  };

  const policy_text = ({ input, meta }) => {
    return (
      <Input
        id="policy_text"
        label="Address Line 1"
        multiline={true}
        rows={4}
        input={input}
        meta={meta}
      />
    );
  };

  const onSubmit = async (values) => {
    let form = values;
    const filteredSelect = policy_data.filter(
      (ele) => ele["sub_type.id"] === select
    );
    form.vendor_complex_id = complex_data.complex_id;
    if (filteredSelect && filteredSelect[0]) {
      form.id = filteredSelect[0].id;
      dispatch(
        step2AddOrUpdate({
          add: 0,
          data: form,
        })
      );
      loadInitialValues();
    } else {
      dispatch(
        step2AddOrUpdate({
          add: 1,
          data: form,
        })
      );
      loadInitialValues();
    }
  };

  return (
    <>
      <Form
        initialValues={globalForm}
        onSubmit={onSubmit}
        render={({ handleSubmit, submitting, values }) => (
          <form onSubmit={handleSubmit}>
            <Grid container className={classes.grid1}>
              <Grid className={classes.grid2}>
                <Typography className={classes.typo3}>Policy Type</Typography>
                <Field
                  name="policy_type_id"
                  component={policy_type_id}
                  placeholder="Select Policy Type"
                  validate={required}
                />
              </Grid>
            </Grid>
            <Grid container className={classes.grid1}>
              <Grid className={classes.grid2}>
                <Typography className={classes.typo3}>policy text</Typography>
                <Field
                  name="policy_text"
                  component={policy_text}
                  placeholder="Policy Text"
                  validate={required}
                />
              </Grid>
            </Grid>
            <Grid container className={classes.grid1}>
              <Grid className={classes.grid2}>
                <Submit
                  title="submit"
                  type="submit"
                  width="110px"
                  disabled={submitting ? submitting : false}
                />
              </Grid>
            </Grid>
            <Grid
              className={classes.paddingTop}
              container
              direction="row"
              justifyContent="flex-start"
              alignItems="center"
            >
              <Submit
                handleclick={() => dispatch(setSteps(steps - 1))}
                title="back"
                width="110px"
              />
              <Submit
                handleclick={() => dispatch(setSteps(steps + 1))}
                title="next"
                width="110px"
              />
            </Grid>
          </form>
        )}
      />
    </>
  );
};

export default Step2;
