import React from "react";
import { useHistory } from "react-router-dom";
import { Form, Field } from "react-final-form";
import { useDispatch, useSelector } from "react-redux";
import { makeStyles } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import Paper from "@material-ui/core/Paper";
import Card from "@material-ui/core/Card";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Link from "@material-ui/core/Link";

import Logo from "../../assets/images/logo_landscape.png";

import Input from "../common/Input/LoginOutlinedInput";
import Submit from "../common/Button/BlueContainedRounded";

import { getCookie, isAuth } from "../../assets/helper/auth";
import { loginSubmitThunk } from "../../redux/features/Auth";
import { siteUrl } from "../../assets/helper/constants";

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: "#fff",
  },
  paper: {
    backgroundColor: `#132252`,
    height: `100vh`,
    width: `100%`,
  },
  card: {
    paddingTop: theme.spacing(4),
    marginBottom: theme.spacing(5),
    width: "100%",
    height: "100%",
    backgroundColor: "transparent",
    border: "none",
  },
  card2: {
    padding: theme.spacing(6.5),
    width: "584px",
    borderRadius: 10,
    [theme.breakpoints.down("xs")]: {
      width: "384px",
    },
  },
  typo1: {
    color: "#272727",
    textTransform: "uppercase",
    fontWeight: "bold",
  },
  typo2: {
    fontSize: "15px",
    fontWeight: 500,
    marginTop: theme.spacing(4),
    textTransform: "uppercase",
    color: "#484848",
  },
  typo3: {
    color: "#808080",
    fontSize: "12px",
    marginTop: theme.spacing(0.5),
    textDecoration: "underline",
  },
  typo4: {
    color: "#fff",
    fontSize: "19px",
    fontWeight: 600,
    textAlign: "center",
    marginTop: theme.spacing(4),
  },
  paddingTop: {
    paddingTop: theme.spacing(2),
  },
}));

const Login = () => {
  const dispatch = useDispatch();
  const { token } = useSelector((state) => state.auth);
  const history = useHistory();
  const classes = useStyles();

  const loginCheck = () => {
    if (isAuth() && isAuth().role_id === 2 && getCookie("token")) {
      window.location = siteUrl + "slots";
      // history.push("/slots");
    } else if (isAuth() && isAuth().role_id === 1 && getCookie("token")) {
      window.location = siteUrl + "vendor-list";
      // history.push("/vendor-list");
    }
  };

  React.useEffect(() => {
    loginCheck();
  }, [token]);

  const required = (value) => (value ? undefined : "Required");

  const email = ({ input, meta }) => {
    return (
      <Input
        id="email"
        label="example@playmaker.in"
        input={input}
        meta={meta}
      />
    );
  };

  const password = ({ input, meta }) => {
    return (
      <Input
        id="password"
        label="Enter your password"
        input={input}
        meta={meta}
        type="password"
      />
    );
  };

  const onSubmit = async (values) => {
    dispatch(loginSubmitThunk(values));
  };

  return (
    <>
      <CssBaseline />
      <Form
        onSubmit={onSubmit}
        render={({ handleSubmit, submitting }) => (
          <form onSubmit={handleSubmit}>
            <Paper className={classes.paper}>
              <Grid
                container
                direction="column"
                alignItems="center"
                justifyContent="center"
              >
                <Grid item>
                  <Card elevation={0} className={classes.card}>
                    <img height={50} src={Logo} alt="logo" />
                  </Card>
                </Grid>
                <Grid item>
                  <Card className={classes.card2}>
                    <Grid>
                      <Typography className={classes.typo1}>
                        welcome to playmaker portal!
                      </Typography>
                    </Grid>
                    <Grid>
                      <Typography className={classes.typo2}>email</Typography>
                    </Grid>
                    <Grid>
                      <Field
                        name="email"
                        component={email}
                        placeholder="Enter Email"
                        validate={required}
                      />
                    </Grid>
                    <Grid>
                      <Typography className={classes.typo2}>
                        password
                      </Typography>
                    </Grid>
                    <Grid>
                      {" "}
                      <Field
                        name="password"
                        component={password}
                        placeholder="Enter Password"
                        validate={required}
                      />
                    </Grid>
                    <Grid>
                      <Typography className={classes.typo3}>
                        <Link href="#" color="inherit">
                          Forgot Password?
                        </Link>
                      </Typography>
                    </Grid>
                    <Grid
                      className={classes.paddingTop}
                      container
                      direction="row"
                      justifyContent="flex-end"
                      alignItems="center"
                    >
                      <Submit
                        title="log in"
                        type="submit"
                        width="110px"
                        disabled={submitting ? submitting : false}
                      />
                    </Grid>
                  </Card>
                  <Grid>
                    <Typography className={classes.typo4}>
                      Want to know more about Playmaker? <br /> Mail us at-
                      partnerships@playmakerapp.in
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
            </Paper>
          </form>
        )}
      />
    </>
  );
};

export default Login;
