import React from "react";

import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import { Divider } from "@material-ui/core";
import IconButton from "@material-ui/core/IconButton";
import ControlPointIcon from "@material-ui/icons/ControlPoint";
import TextField from "@material-ui/core/TextField";

import SelectPricing from "../common/SelectPricing";
import CourtButton from "../common/CourtButton";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(3),
    backgroundColor: "#F5F5F5",
    borderColor: "#fff",
    display: "flex",
  },
  paper2: {
    padding: theme.spacing(2),
    marginLeft: theme.spacing(3),
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    backgroundColor: "#F3F3F3",
  },
  typo1: {
    color: "#484848",
    verticalAlign: "middle",
    fontWeight: "bold",
  },
  typo2: {
    paddingBottom: theme.spacing(1),
    color: "#484848",
    verticalAlign: "middle",
    fontWeight: "bold",
  },
  typo3: {
    paddingBottom: theme.spacing(1),
    color: "#808080",
    verticalAlign: "middle",
  },
  leftMargin: {
    marginLeft: theme.spacing(3),
    marginTop: theme.spacing(2),
  },
  divider: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  grid1: {
    display: "flex",
  },
  right: {
    marginLeft: "auto",
  },
  icon: {
    color: "#272727",
    marginLeft: "auto",
  },
  textfield: {
    backgroundColor: "#fff",
    width: "95%",
    marginBottom: theme.spacing(1),
  },
  paddingLeft: {
    paddingLeft: theme.spacing(3),
  }
}));

const weekData = [
  {
    id: 1,
    name: "Monday",
  },
  {
    id: 2,
    name: "Tuesday",
  },
  {
    id: 3,
    name: "Wednesday",
  },
  {
    id: 4,
    name: "Thursday",
  },
  {
    id: 5,
    name: "Friday",
  },
  {
    id: 6,
    name: "Saturday",
  },
  {
    id: 7,
    name: "Sunday",
  },
];

const sportData = [
  {
    id: 1,
    name: "Football 5-aSide",
  },
  {
    id: 2,
    name: "Hockey 6-aSide",
  },
  {
    id: 3,
    name: "Cricket 7-aSide",
  },
];

const turfData = [
  {
    id: 1,
    name: "Turf 1",
  },
  {
    id: 2,
    name: "Turf 2",
  },
  {
    id: 3,
    name: "Turf 3",
  },
];

const AddPricing = () => {
  const classes = useStyles();

  const getSelectValue = (value) => {
    console.log("value :>> ", value);
  };

  const Slots = () => {
    return (
      <Grid lg={6} md={12}>
        <Paper className={classes.paper2} elevation={0}>
          <form>
            <Grid lg={12} className={classes.grid1}>
              <Grid lg={4}>
                <Typography className={classes.typo3}>Start Time</Typography>
                <TextField
                  className={classes.textfield}
                  id="start"
                  label="6:00 PM"
                  type="input"
                  variant="outlined"
                />
              </Grid>
              <Grid lg={4}>
                <Typography className={classes.typo3}>Start Time</Typography>
                <TextField
                  className={classes.textfield}
                  id="end"
                  label="4:00 PM"
                  type="input"
                  variant="outlined"
                />
              </Grid>
              <Grid lg={4}>
                <Typography className={classes.typo3}>Start Time</Typography>
                <TextField
                  className={classes.textfield}
                  id="volumedisc"
                  label="Slot"
                  type="input"
                  variant="outlined"
                />
              </Grid>
            </Grid>
            <Grid lg={12} className={classes.grid1}>
              <Grid lg={4}>
                <Typography className={classes.typo3}>Hourly Pricing</Typography>
                <TextField
                  className={classes.textfield}
                  id="hourprice"
                  label="450"
                  type="input"
                  variant="outlined"
                />
              </Grid>
              <Grid lg={4}>
                <Typography className={classes.typo3}>Slot Extension Pricing</Typography>
                <TextField
                  className={classes.textfield}
                  id="slotext"
                  label="500"
                  type="input"
                  variant="outlined"
                />
              </Grid>
              <Grid lg={4}>
                <Typography className={classes.typo3}>Discount</Typography>
                <TextField
                  className={classes.textfield}
                  id="disc"
                  label="%"
                  type="input"
                  variant="outlined"
                />
              </Grid>
            </Grid>
          </form>
        </Paper>
      </Grid>
    );
  };

  return (
    <Grid>
      <Grid>
        <Paper className={classes.paper} variant="outlined">
          <Typography variant="h6" className={classes.typo1}>
            Pricing Details
          </Typography>
        </Paper>
      </Grid>
      <Divider className={classes.divider} />
      <Grid className={classes.paddingLeft}>
        <SelectPricing
          data={weekData}
          funct="select"
          title="Select"
          getSelectValue={(value) => {
            getSelectValue(value);
          }}
        />
        <SelectPricing
          data={sportData}
          funct="sports"
          title="Sports"
          getSelectValue={(value) => {
            getSelectValue(value);
          }}
        />
      </Grid>
      <Divider className={classes.divider} />
      <Grid className={classes.leftMargin} lg={6}>
        <Typography className={classes.typo2}>Select Turf/Court</Typography>
        <CourtButton turfData={turfData} />
      </Grid>
      <Divider className={classes.divider} />

      <Grid lg={6} md={12} className={classes.grid1, classes.paddingLeft}>
        <Grid>
          <SelectPricing
            data={weekData}
            funct="from"
            title="From"
            getSelectValue={(value) => {
              getSelectValue(value);
            }}
          />
          <SelectPricing
            data={weekData}
            funct="to"
            title="To"
            getSelectValue={(value) => {
              getSelectValue(value);
            }}
          />
        </Grid>
        <Grid className={classes.right}>
          <IconButton className={classes.icon} aria-label="create pricing" component="span">
            <ControlPointIcon />
            <Typography className={classes.typo2}>ADD SLOTS</Typography>
          </IconButton>
        </Grid>
      </Grid>
      <Slots />
      <Divider />
      <Slots />
    </Grid>
  );
};

export default AddPricing;
